import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Snackbar from '@material-ui/core/Snackbar';
import MySnackbarContent from '../../shared/message';
//import shared
import Message from '../../shared/message';
//componentes de la vista
import InfoUser from '../componentes/info-user';
import FormCompra from '../componentes/form-compra';
import DetallePresupuesto from '../componentes/detallePresupuesto/detallePresupuesto';
import FormTerminosReferencia from '../componentes/form-terminosReferencia';
import FormEntregables from '../componentes/form-entregables';
import axios from "axios";
import env from "../../config/env";
import DetallesCompra from '../componentes/detalles/detallesCompra';
import {connect} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';

function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}


  function TabContainer({ children }) {
    return (
      <div style={{ padding: 8 * 3 }}>
        {children}
      </div>
    );
  }
  TabContainer.propTypes = {
  children: PropTypes.node.isRequired
};
const Styles = ({
  cont: {
    display: 'flex',
    justifyContent: 'center'
  },

});
const styles = theme => ({
  
  root: {
    width: '98%',
  },
  cont: {
    display: 'flex',
    justifyContent: 'center'
  },
   content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 3,
  },
  toolbar: theme.mixins.toolbar,

});

function getSteps() {
  return ['Datos Solicitante ', 'Datos generales de compra', 'Presupuesto', 'Terminos de referencia.', 'Entregables'];
}
class PageCompraForm extends React.Component {
  


  componentDidMount() {
    this.ValidarPresupuesto();
  }
  getStepContent(stepIndex) {
    switch (stepIndex) {
      case 0:
     return <InfoUser btns={this.btns} USER={this.useradd}  userData={this.state.user} compra={this.state.compra} compraData={this.compra} />;  //falta que pudan escoger otra jajaa
      case 1:
        return this.state.user.id? <FormCompra handleBack={this.handleBack} handleNext={this.handleNext} compra={this.state.compra} compraData={this.compra} msgbn={this.msgbn} btns={this.btns} user={this.state.user}/>: 'Error se requiere un persona que rea responsable de esta compra';
      case 2:
        return <DetallePresupuesto periodo={this.state.peridoPresupuesto} compra={this.state.compra} msgbn={this.msgbn} CanNext={this.CanNext}   btns={this.btns}/> 
      case 3:
        return <FormTerminosReferencia compra={this.state.compra} msgbn={this.msgbn} CanNext={this.CanNext}   btns={this.btns} />;
      case 4:
        return <FormEntregables compra={this.state.compra} msgbn={this.msgbn} CanNext={this.CanNext}   btns={this.btns} />;
      default:
        return <Message />;
    }
  }
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
      compra: {},
      user:{id: 0}, 
      open: false,
      msgMo: '',
      peridoPresupuesto:{}, 
      Tipo: 'success',
      error:{}, 
      canNext: true,
      stateBtn: true,
    }
    this.handleNext = this.handleNext.bind(this);
    this.handleBack = this.handleBack.bind(this);


  }
  handleNext = () => {
    this.setState(state => ({
      activeStep: state.activeStep + 1,
    }));
  };

  handleBack = () => {
    this.setState(state => ({
      activeStep: state.activeStep - 1,
    }));
  };

  handleReset = () => {
    this.setState({
      activeStep: 0,
    });
  };
  compra = (data) => {
    this.setState(state => ({
      compra: data,
    }));
  }
  useradd = (data) => {
    this.setState(state => ({
      user: data,
    }));
    this.handleNext();
  }

  CanNext = (estado) => {
    this.setState({
      canNext: estado,
    });

  }
  btns =(estado)=> {
    this.setState({
      stateBtn: estado,
    })
  }
  msgbn = (mensage) => {
    this.setState({
      open: true,
      msgMo: mensage
    });
  }
  Publicar=()=>{
    this.getPublicar();
  }
  async ValidarPresupuesto() {
    try {
 let res=   await axios({
        url: env.endpoint + '/Presupuesto' ,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      this.setState({
        peridoPresupuesto:res.data
      })
    }
    catch (error) {
     console.log(error.response);
     if (error.response.status === 401){
this.setState({
  error:{error:true, menssage: error.response.data.errors}
})
     }
    }
  }
  async getPublicar() {
    try {
    await axios({
        url: env.endpoint + '/publicar/' + this.state.compra.id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      this.Redirigir();
    }
    catch (error) {
     console.log(error.response.data.message);
    }
  }
Redirigir(){  
  this.props.history.push('/propuesta/'+ this.state.compra.id);
}

  render() {
    const { classes } = this.props;
    const steps = getSteps();
    const { activeStep } = this.state;
    return (this.state.peridoPresupuesto.id?
          <TabContainer>
        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={this.state.open}
          autoHideDuration={6000}
          onClose={this.handleClose}
        >
          <MySnackbarContent
            onClose={this.handleClose}
            variant={this.state.Tipo}
            message={this.state.msgMo}
          />
        </Snackbar>
    <p>Presupuesto: {this.state.peridoPresupuesto.nombre}</p>
    <Stepper activeStep={activeStep} alternativeLabel>
          {steps.map(label => {
            return (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            );
          })}
        </Stepper>
        <div className={classes.toolbar}>
          {this.state.activeStep === steps.length ? (
            <div >
              <Typography className={classes.instructions}> Por favor revisar si todo se encuentra Bien de lo contrario corregir.</Typography>
                  <DetallesCompra  COMPRADATA={this.state.compra} />

                  <div className={classes.cont} >
              <Button onClick={this.handleReset}>Volver</Button>
              <Button variant="contained" color="primary"   onClick={this.Publicar}  >Confirmar</Button>
              </div>
            </div>
          ) :
            this.state.stateBtn === true ? (
              <div   >
                <div >{this.getStepContent(activeStep)}</div>
                <div className={classes.cont} >
                  <Button
                    disabled={activeStep === 0}
                    onClick={this.handleBack}
                    className={classes.backButton}
                  >
                    Back
                 </Button>
                  <Button disabled={!this.state.canNext} variant="contained"
                    color="primary" onClick={this.handleNext}>
                    {activeStep === steps.length - 1 ? 'Finish' : 'Siguiente'}
                  </Button>
                </div>
              </div>
            ) : (
              <div >{this.getStepContent(activeStep)}</div>
              )}
        </div>
      </TabContainer>:  this.state.error.error? <div style={Styles.cont} >{this.state.error.menssage }</div>:
      <div style={Styles.cont} > <CircularProgress className={classes.progress} color="secondary" /></div>
    );
  }
}
PageCompraForm.propTypes = {
  classes: PropTypes.object,
};

export default connect(mapSteToProps)(withStyles(styles)(PageCompraForm));