import React from 'react';
import TextField from '@material-ui/core/TextField';
import env from "../../config/env";
import axios from "axios";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import { store } from '../../../store/store';

function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}


const styles = ({
  cont: {
    display: 'flex',
    justifyContent: 'center'
  },

});
class FormCompra extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      NombreCompra: '',
      Objetivo: '',
      FechaInicio: '',
      FechaFin: '',
      msg: {
        estado: false,
        msg: '',
        tipo: ''
      },
      error: {},
      canSubmit: true,
      action: true,
    };
  }

  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }


  syncField(ev, fieldName) { // data form
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
  }

  componentDidMount() {
    this.getValidate();
    this.props.btns(false);
  }
  getValidate() {
    this.setState({
      action: false
    })
    if (this.props.compra.id != null) {
      this.getData(this.props.compra.id);
    } else {
      this.setState({
        action: true
      })
    }
  }


  async getData(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/compras/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        console.log( new Date(res.data.FechaFin).toISOString().split('T')[0]);
        // peticion correctamente
        this.setState({
          action: false,
          NombreCompra: res.data.NombreCompra,
          Objetivo: res.data.objetivo,
          FechaFin: new Date(res.data.FechaFin).toISOString().split('T')[0],
          FechaInicio: new Date(res.data.FechaInicio).toISOString().split('T')[0]
        })
      }
      console.log(this.state.FechaInicio);
      // Don't forget to return something   
     // return res.data
    }
    catch (error) {
      //this.getError(error.response);
    }
  }

  handleBack = () => {
    this.props.handleBack();
  }
  onSubmit = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'post',
      url: env.endpoint + '/compras',
      data: {
        NombreCompra: this.state.NombreCompra,
        objetivo: this.state.Objetivo,
        FechaInicio: this.state.FechaInicio,
        FechaFin: this.state.FechaFin,
        user_id: this.props.user.id, //revisar menera que no se haga haci
      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }

    }).then(response => {

      this.props.compraData(response.data); //Data
      this.setState({ canSubmit: true });
      this.props.handleNext();

    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }
  onSubmitEditar = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'put',
      url: env.endpoint + '/compras/' + this.props.compra.id,
      data: {
        NombreCompra: this.state.NombreCompra,
        objetivo: this.state.Objetivo,
        FechaInicio: this.state.FechaInicio,
        FechaFin: this.state.FechaFin,
        user_id: this.props.user.id,
      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }

    }).then(response => {
      //console.log(response);
      this.props.compraData(response.data);
      this.setState({ canSubmit: true });

      this.props.handleNext();
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }



  render() {
    return (<div >
      <Grid container spacing={24}>

        <Grid item xs={12} sm={6}>
          <TextField
            error={this.state.error.NombreCompra ? true : false}
            label={this.state.error.NombreCompra ? this.state.error.NombreCompra[0]:"Nombre Compras"}
            value={this.state.NombreCompra}
            placeholder="Nombre compra"
            ref="NombreCompra"
            name="NombreCompra"
            onChange={(e) => this.syncField(e, 'NombreCompra')}
            fullWidth={true}
            multiline
            margin="normal"
          />
          <TextField
           error={this.state.error.objetivo ? true : false}
           label={this.state.error.objetivo ? this.state.error.objetivo[0]:"Objetivo compra"}
            placeholder="Objetivo compra"
            value={this.state.Objetivo}
            ref="Objetivo"
            name="Objetivo"
            multiline
            onChange={(e) => this.syncField(e, 'Objetivo')}
            fullWidth={true}
            margin="normal"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
           error={this.state.error.FechaInicio ? true : false}
           label={this.state.error.FechaInicio ? this.state.error.FechaInicio[0]:"Fecha de inicio"}
           id="dateini"
            type="date"
            ref="FechaInicio"
            name="FechaInicio"
            value={this.state.FechaInicio}
            onChange={(e) => this.syncField(e, 'FechaInicio')}
            fullWidth={true}
            margin="normal"
            InputLabelProps={{
              shrink: true,
            }}
          />
          <TextField
            error={this.state.error.FechaFin ? true : false}
            label={this.state.error.FechaFin ? this.state.error.FechaFin[0]:"Fecha de finalizacin"}
            id="date"
            type="date"
            ref="FechaFin"
            name="FechaFin"
            Value={this.state.FechaFin}
            onChange={(e) => this.syncField(e, 'FechaFin')}
            fullWidth={true}
            margin="normal"
            InputLabelProps={{
              shrink: true,
            }}
          />
        </Grid>
      </Grid>
      <div style={styles.cont} >
        <Button onClick={this.handleBack}> atras  </Button>
        {this.state.action ? <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit}> Registrar  </Button> :
          <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar}> Modificar  </Button>
        }
      </div>

    </div>);
  }
}

export default connect(mapSteToProps)(FormCompra);