import React from 'react';
import TextField from '@material-ui/core/TextField';
import env from "../../config/env";
import axios from "axios";
import FormControlLabel from '@material-ui/core/FormControlLabel';
import InputLabel from '@material-ui/core/InputLabel';
import Switch from '@material-ui/core/Switch';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import { store } from '../../../store/store';
import Icon from '@material-ui/core/Icon';
import CircularProgress from '@material-ui/core/CircularProgress';

function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}

class FormConflictos extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: '',
      comentarios: '',
      acepta: '',
      estado: false,
      msg: {
        estado: false,
        msg: '',
        tipo: ''
      },
      canSubmit: true,
      action: true,
    };
  }


  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }


  componentDidMount() {
    this.getData();

  }

  async getData() {
    try {
      let res = await axios({
        url: env.endpoint + '/politica/' + this.props.politica.id + '/' + this.props.propuesta.id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({
          action: false,
          id: res.data.id,
          comentarios: res.data.comentarios,
          estado: res.data.acepta
        })
      }
    }
    catch (error) {
      console.log(error);
      // this.getError(error.response);
    }
  }


  onSubmit = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'post',
      url: env.endpoint + '/detallepoliticas',
      data: {
        comentarios: this.state.comentarios,
        acepta: this.state.estado ? 1 : 0,
        politica_id: this.props.politica.id,
        proposal_id: this.props.propuesta.id,

      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }

    }).then(response => {

      this.setState({
        action: false,
        canSubmit: true,
        id: response.data.id,
        comentarios: response.data.comentarios,
        estado: response.data.acepta
      })

    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }


  onSubmitEditar = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'put',
      url: env.endpoint + '/detallepoliticas/' + this.state.id,
      data: {
        comentarios: this.state.comentarios,
        acepta: this.state.estado ? 1 : 0,
      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }

    }).then(response => {

      this.setState({
        action: false,
        canSubmit: true,
        id: response.data.id,
        comentarios: response.data.comentarios,
        estado: response.data.acepta
      })
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }
  updateStado = () => {
    this.setState({
      estado: !this.state.estado,
    });
  }
  syncField(ev, fieldName) {
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
  }
  render() {

    return (<div >
      <Grid container spacing={24}>
        <Grid item xs={12} sm={12}>
          {this.props.politica.pregunta}
        </Grid>

        <Grid item xs={12} sm={6}>
          <TextField
            label="Observaciones"
            value={this.state.comentarios}
            placeholder="Observaciones"
            ref="Observaciones"
            name="Observaciones"
            onChange={(e) => this.syncField(e, 'comentarios')}
            fullWidth={true}
            margin="normal"
            multiline
          //variant="outlined"// 
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <InputLabel htmlFor="estado">{this.state.estado ? 'SI' : 'NO'} </InputLabel>
          <FormControlLabel
            control={
              <Switch
                checked={this.state.estado}
                onChange={(e) => this.updateStado(e, 'estado')}
                value='activo'
              />
            }
          />
          {
            !this.state.canSubmit ? <CircularProgress color="secondary" /> : (

              this.state.action ? (<Icon variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit} >done_all</Icon>) :
                (<Icon variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar} >done_all</Icon>)
            )}
        </Grid>

      </Grid>


    </div>);

  }

}
export default connect(mapSteToProps)(FormConflictos);