import React from 'react';

import TextField from '@material-ui/core/TextField';
import env from "../config/env";
import axios from "axios";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import {store} from '../../store/store';
import ListProvedorData from './list-data-provedor';


function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}
class FormProvedor extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      nombre: '',
      representate: '',
      email: '',
      telefono: '',
      nit: '',
      direccion: '',
      ciudad: '',
      error:{},
      provedores:{data:{}}, 
      activePage: 1, 
      msg: {
        estado: false,
        msg: '',
        tipo: ''
      },
      canSubmit: true,
      action: true,
    };
  }
  componentDidMount() {
    this.getdataProvedor(1);
    
    }
  


  syncField(ev, fieldName) {
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
  }

  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      case 404:
        console.log("error 404, archivo no encontrado")
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }


  onSubmit = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'post',
      url: env.endpoint + '/empresas',
      data: {
        nombre: this.state.nombre,
        nit: this.state.nit,
        representate: this.state.representate,
        email: this.state.email,
        direccion: this.state.direccion,
        telefono: this.state.telefono,
        ciudad: this.state.ciudad

      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }

    }).then(response => {
      //console.log(response.data);
      this.clear();
//console.log(this.props.father);
      switch(this.props.father){// ? llega el prop de default segun  lo que lo utilize
        case 'propuesta': //modificamos el  estado del 
        this.props.provedor(response.data);
           this.props.handleNext();
        break;
        default:
        this.props.handleNext();
        break
      }
    })
      .catch(error => {
        this.setState({ canSubmit: true });
       this.getError(error.response);
      });
  }

clear(){
  this.setState({ canSubmit: true , 
    action: true,
    nombre: '',
    nit: '',
    representate: '',
    email: '',
    direccion: '',
    telefono: '',
    ciudad: ''
  });
  this.getdataProvedor(1);
}

handlePageChange = (pageNumber) => {
  //console.log(`active page is ${pageNumber}`);
  this.setState( {activePage: pageNumber}
    );
    this.getdataProvedor(pageNumber);
}


async getdataProvedor(page) {
  try {
    let res = await axios({
      url: env.endpoint + '/empresas?page='+ page,
      method: 'get',
      timeout: 8000,
      headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    })
    if (res.status === 200) {
      // peticion correctamente
      this.setState({ provedores: res.data });
    }
  }
  catch (error) {
   // this.getError(error.response);
  }
}


modificar = (id) => {
  this.getprovedor(id)
}

async getprovedor(id) { // Obtener los datos para modificar 
  try {
    let res = await axios({
      url: env.endpoint + '/empresas/' + id,
      method: 'get',
      timeout: 8000,
      headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    })
    if (res.status === 200) {
      this.setState({
        error:{}, 
        action: false, 
        id: res.data.id,
        nombre: res.data.nombre,
        nit: res.data.nit,
        representate: res.data.representate,
        email: res.data.email,
        direccion: res.data.direccion,
        telefono: res.data.telefono,
        ciudad: res.data.ciudad 
      })
    }
  } catch (error) {
    this.getError(error.response);
  }
}



onSubmitEditar = (e) => {
  this.setState({ canSubmit: false });
  axios({
    method: 'put',
    url: env.endpoint + '/empresas/'+ this.state.id,
    data: {
      nombre: this.state.nombre,
      nit: this.state.nit,
      representate: this.state.representate,
      email: this.state.email,
      direccion: this.state.direccion,
      telefono: this.state.telefono,
      ciudad: this.state.ciudad
    }, headers: {
      'Accept': '*/*',
      'Authorization': this.props.userAcceso
    }

  }).then(response => {
    this.clear();
  })
    .catch(error => {
      this.setState({ canSubmit: true });
     this.getError(error.response);
    });
}


  render() {
    return (<div >
      <Grid container spacing={24}>
        <Grid item xs={12} sm={6}>
          <TextField
            error={this.state.error.nombre ? true : false}
            label={this.state.error.nombre ? this.state.error.nombre[0] : "Nombre Empresa"}
            value={this.state.nombre}
            placeholder="Nombre Empresa"
            ref="NombreEmpresa"
            name="NombreEmpresa"
            onChange={(e) => this.syncField(e, 'nombre')}
            fullWidth={true}
            margin="normal"
          />
          <TextField
           error={this.state.error.representate ? true : false}
           label={this.state.error.representate ? this.state.error.representate[0] : "Representante Legal"}
            placeholder="Representante Legal"
            value={this.state.representate}
            ref="representate"
            name="representate"
            onChange={(e) => this.syncField(e, 'representate')}
            fullWidth={true}
            margin="normal"
          />
          <TextField
           error={this.state.error.telefono ? true : false}
           label={this.state.error.telefono ? this.state.error.telefono[0] : "Telefono"}
            placeholder="Telefono"
            value={this.state.telefono}
            ref="telefono"
            name="telefono"
            onChange={(e) => this.syncField(e, 'telefono')}
            fullWidth={true}
            margin="normal"
          />

          <TextField
          error={this.state.error.email ? true : false}
          label={this.state.error.email ? this.state.error.email[0] : "Email contacto"}
            placeholder="Email contacto"
            value={this.state.email}
            ref="email"
            name="email"
            onChange={(e) => this.syncField(e, 'email')}
            fullWidth={true}
            margin="normal"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
           error={this.state.error.nit ? true : false}
           label={this.state.error.nit ? this.state.error.nit[0] : "NIT"}
            value={this.state.nit}
            placeholder="NIT"
            ref="nit"
            name="nit"
            onChange={(e) => this.syncField(e, 'nit')}
            fullWidth={true}
            margin="normal"
          />
          <TextField
            error={this.state.error.direccion ? true : false}
            label={this.state.error.direccion ? this.state.error.direccion[0] : "Direccin"}
            placeholder="Direccin"
            value={this.state.direccion}
            ref="direccion"
            name="direccion"
            onChange={(e) => this.syncField(e, 'direccion')}
            fullWidth={true}
            margin="normal"
          />
          <TextField
            error={this.state.error.ciudad ? true : false}
            label={this.state.error.ciudad ? this.state.error.ciudad[0] : "Ciudad"}
            placeholder="Ciudad"
            value={this.state.ciudad}
            ref="ciudad"
            name="ciudad"
            onChange={(e) => this.syncField(e, 'ciudad')}
            fullWidth={true}
            margin="normal"
          />
        </Grid>

      </Grid>
      {this.state.action ? <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit}> Registrar  </Button> :
        <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar}> Modificar  </Button>
      }

{!this.props.father?
  <ListProvedorData   datos={this.state.provedores} handlePageChange={this.handlePageChange} activePage={this.state.activePage}    modificar={this.modificar} />

: ''}
    </div>
    );
  }

}
export default connect(mapSteToProps)(FormProvedor);