import React from 'react';
import axios from 'axios';
import env from "../../config/env";
import { connect } from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import FormConflictos from './form-confictos';
import ListView from './list-view';


function mapSteToProps(state) {
    return { userAcceso: state.userAcceso }
}
const Styles = ({

    cont: {
        display: 'flex',
        justifyContent: 'center'
    },
});

class ListConfictos extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            politicas: [],
            msg: {
                estado: false,
                msg: '',
                tipo: ''
            },
            canSubmit: true,
            action: true,
        };
    }


    syncField(ev, fieldName) {
        let element = ev.target;
        let value = element.value;
        let jsonState = {};
        jsonState[fieldName] = value;
        this.setState(jsonState);
    }
    componentDidMount() {
        this.getPoliticas();
    }
    async getPoliticas() {
        try {
            let res = await axios({
                url: env.endpoint + '/politicas',
                method: 'get',
                timeout: 8000,
                headers: {
                    'Accept': '*/*',
                    'Authorization': this.props.userAcceso
                }
            })
            if (res.status === 200) {
                // peticion correctamente
                this.setState({
                    politicas: res.data
                })
            }
        }
        catch (error) {
            this.getError(error.response);
        }
    }
    acciones() {
        if (this.props.form) {
            return this.setListP();
        } else {
            return this.setListView();
        }
    }
    setListView() {
        if (this.state.politicas.length > 0) {
            return this.state.politicas.map(row => {
                return (<div key={row.id} ><ListView politica={row} propuesta={this.props.propuesta} /> <br></br></div>);
            });
        } else {
            return <div style={Styles.cont} ><CircularProgress color="secondary" /> </div>;
        }
    }

    setListP() {
        if (this.state.politicas.length > 0) {
            if (this.state.politicas) {
                return this.state.politicas.map(row => {
                    return (<div key={row.id} ><FormConflictos politica={row} propuesta={this.props.propuesta} /> <br></br></div>);
                });
            }
        } else {
            return <div style={Styles.cont} ><CircularProgress color="secondary" /> </div>;
        }
    }
    render() {
        return (<div> {this.acciones()}</div>);
    }
}
export default connect(mapSteToProps)(ListConfictos);