import React from 'react';
import { Route, Redirect} from 'react-router-dom';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import jwtDecode from "jwt-decode";


const PrivateRouteSuperAdmin = ({ component: Component, isAuthenticated, ...rest }) => (
  <Route
    {...rest}
    render={props => (
      isAuthenticated
        ? (
          <Component {...props} />
        )
        : (<Redirect to={{ pathname: '/home', state: { from: props.location } }} />)
    )}
  />
);
PrivateRouteSuperAdmin.propTypes = {
  isAuthenticated: PropTypes.bool.isRequired,
};
function mapStateToProps(state) {

  if (state.userAcceso !== null && state.userAcceso !== '' && jwtDecode(state.userAcceso).rol === 'Super-Admin') {
    return { isAuthenticated: true, };
  } else {
    return {
      isAuthenticated: false,
    };
  }
}
export default connect(mapStateToProps)(PrivateRouteSuperAdmin);