import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
//imporm componentes
import PageProvedor from '../components/page-provedor';
import PagePoliticas from '../components/politicas/page-politicas';
import FormPropuestaEconomica from '../components/propuesta/form-propuesta-economica';


import { connect } from 'react-redux';


function mapSteToProps(state) {
  return { User: state.UserData, userAcceso: state.userAcceso }
}

const styles = theme => ({
  root: {
    width: '90%',
  },
  button: {
    marginTop: theme.spacing.unit,
    marginRight: theme.spacing.unit,
  },
  actionsContainer: {
    marginBottom: theme.spacing.unit * 2,
  },
  resetContainer: {
    padding: theme.spacing.unit * 3,
  },
});

class PagePropuestaForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      siguiente: false,
      siguiente1: false,
      activeStep: 0,
      provedor: {},
      propuesta: {},
    }
  }
  getSteps() {
    return [
      this.state.activeStep === 0 ? 'Buscar Provedor' : this.state.provedor.nombre + ' NIT: ' + this.state.provedor.nit,
      this.state.activeStep !== 2 ? 'Politicas y servicios.' : 'Radicado propuesta #' + this.state.propuesta.id,
      'Propuesta economica'];
  }
  getStepContent(step) {
    switch (step) {
      case 0:
        return (<PageProvedor
          siguienteUpdate={this.siguienteUpdate}
          handleNext={this.handleNext}
          provedor={this.provedor}
        />);

      case 1:
        return (<PagePoliticas
          siguienteUpdate={this.siguienteUpdate}
          propuesta={this.propuesta}
          provedor={this.state.provedor}
          compra={this.props.compra}
          handleBack={this.handleBack}
          handleNext={this.handleNext}
        />);
      case 2:
        return <FormPropuestaEconomica siguienteUpdate={this.siguienteUpdate} propuesta={this.state.propuesta} />;
      default:
        return 'Unknown step';
    }
  }
  handleNext = () => {
    this.setState(state => ({
      activeStep: state.activeStep + 1,
    }));
  };

  handleBack = () => {
    this.setState(state => ({
      activeStep: state.activeStep - 1,
    }));
  };

  handleReset = () => {
    this.setState({
      activeStep: 0,
    });
  };
  provedor = (data) => {
    this.setState({
      provedor: data,
    });
  };

  propuesta = (data) => {
    this.setState({
      propuesta: data,
    });
  };


  siguienteUpdate = (action) => {
    this.setState({
      siguiente: action,
    });
  };

  render() {
    const { classes } = this.props;
    const steps = this.getSteps();
    const { activeStep } = this.state;
    return (
      <div >
        <Stepper activeStep={activeStep} orientation="vertical">
          {steps.map((label, index) => {
            return (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
                <StepContent>
                  {this.getStepContent(index)}

                  {this.state.siguiente ?
                    <div className={classes.actionsContainer}>
                      <div>

                        <Button
                          disabled={activeStep === 0}
                          onClick={this.handleBack}
                          className={classes.button}
                        >
                          Volver a Propuesta
                      </Button>
                        <Button
                          variant="contained"
                          color="primary"
                          onClick={this.handleReset}
                          className={classes.button}
                        >
                          Nuevo Registro
                        </Button>
                      </div>
                    </div>
                    : ''}
                  {this.state.siguiente1 ?
                    <div className={classes.actionsContainer}>
                      <div>

                        <Button
                          disabled={activeStep === 0}
                          onClick={this.handleBack}
                          className={classes.button}
                        >
                          Back
                      </Button>
                        <Button
                          variant="contained"
                          color="primary"
                          onClick={this.handleNext}
                          className={classes.button}
                        >
                          {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
                        </Button>
                      </div>
                    </div>
                    : ''}
                </StepContent>
              </Step>
            );
          })}
        </Stepper>
        {activeStep === steps.length && (
          <Paper square elevation={0} className={classes.resetContainer}>
            <Typography>All steps completed - you&apos;re finished</Typography>
            <Button onClick={this.handleReset} className={classes.button}>
              Reset
            </Button>
          </Paper>
        )}
      </div>
    );
  }
}
PagePropuestaForm.propTypes = {
  classes: PropTypes.object,
};
export default connect(mapSteToProps)(withStyles(styles)(PagePropuestaForm));