import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import SwipeableViews from 'react-swipeable-views';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import PagePropuestaForm from './page-propuesta-form';
import PagePropuestacontainer from '../components/propuesta/page-propuesta-componente';
import DetallesCompra from '../../compra/componentes/detalles/detallesCompra';
import CircularProgress from '@material-ui/core/CircularProgress';
import env from "../../config/env";
import axios from "axios";
import { connect } from 'react-redux';


const Styles =  ({
  cont: {
    display: 'flex',
    justifyContent: 'center'
  },
});

function mapSteToProps(state) {
  return { User: state.UserData, userAcceso: state.userAcceso }
}


function TabContainer({ children }) {
  return (
    <div style={{ padding: 8 * 3 }}>
      {children}
    </div>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired
};

const styles = theme => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: 500,
  },
});

class PagePropuesta extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      compra: {},
      value: 0,
      presupuestoAfect:{data:[]}, 
    }
  };


  componentDidMount() {
    if (this.props.match.isExact) {
      this.getData(this.props.match.params.idCompra);
    } else {
      this.props.history.push('/home')
    }
  }


  modificarInicio = () => {
    this.getData(this.props.match.params.idCompra);
  }



  async getPresupuestoAfectado (id){ 
    try {
      let res = await axios({
        url: env.endpoint + '/listdetallePresupuesto/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ presupuestoAfect: res.data });
      
      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      this.getError(error.response);
    }
  }





  async getData(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/compras/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      // peticion correctamente
      //  console.log(res.data);
      this.setState({
        compra: res.data,
      })
      if(res.data.EstadoAction === 3 ){
        this.getPresupuestoAfectado (res.data.id);
      }
    }
    catch (error) {
      this.props.history.push('/home')
    }
   
  }
  handleChange = (event, value) => {
    this.setState({ value });
  };
  handleChangeIndex = index => {
    this.setState({ value: index });
  };


  getEstado(estado){
     switch(estado){
      case 0:
      return 'Compra registrada'
      case 1:
      return 'Compra anulada'
      case 2:
      return 'compra habilitada para resivir propuesta'
      case 3:
      return 'compra aprobada'
      case 4:
      return 'compra habilitada'
      default:
      return ''
    }
  }
  render() {
    const { theme } = this.props;
    return (
      <div >
        <Paper >
          <Tabs
            value={this.state.value}
            onChange={this.handleChange}
            indicatorColor="primary"
            textColor="primary"
            centered
          >
            <Tab label="Compra" />
            <Tab label="Propuestas" />
            
            {this.state.compra.EstadoAction ===2?
             <Tab label="Registrar"  />  :
              ''}
            
          </Tabs>
        </Paper>
         
       {this.getEstado(this.state.compra.EstadoAction)}
       
       
        <SwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={this.state.value}
          onChangeIndex={this.handleChangeIndex}
        >
{
  /**
   * containes donde se mostrara la compra, propuesta y registro de propuestas.
   */
}
  
          <TabContainer >
       
            {this.state.compra.id > 0 ?
              <DetallesCompra presupuestoAfect={this.state.presupuestoAfect} COMPRADATA={this.state.compra} /> :
              <div style={Styles.cont} >   <CircularProgress  color="secondary" /> </div>
            }
          </TabContainer>
          <TabContainer >
            {this.state.compra.id > 0 ?
              <PagePropuestacontainer inicio={this.modificarInicio} compra={this.state.compra} /> :
              <div style={Styles.cont} >   <CircularProgress  color="secondary" /> </div>
            }

          </TabContainer>
          {this.state.compra.EstadoAction ===2?
          <TabContainer > 
             < PagePropuestaForm compra={this.state.compra} />
          </TabContainer>:''}
        </SwipeableViews>
      </div>
    );
  }
}
PagePropuesta.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default connect(mapSteToProps)(withStyles(styles, { withTheme: true })(PagePropuesta));