import React from 'react';



class MSGEstado extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
        }
    }

    acccion(){
        switch(this.props.estado){
            case 0:
            return <p>Registrada</p>;
            case 1:
            return <p>Anulada</p>
            case 2:
            return <p>Resive propuestas</p>
            case 3:
            return <p>Aprobada</p>
            case 4:
            return <p>Esperando Aprobacin- cerrada </p>
            default:
            return <p>Error</p>
        }

    }


render() {
return this.acccion();
}



}


export default MSGEstado;