import React from 'react';
import TextField from '@material-ui/core/TextField';
import env from "../../config/env";
import axios from "axios";
import Button from '@material-ui/core/Button';
import TerminosReferencia from './terminosReferencia';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import { store } from '../../../store/store';



function mapSteToProps(state) {
  return {  userAcceso: state.userAcceso }
}

class FormTerminosReferencia extends React.Component {


  constructor(props) {
    super(props);
    this.state = {
      id:'', 
      Especificacion: '',
      observaciones: '',
      cantidad: 0,
      datos: [],
      action: true,
      msg: {
        estado: false,
        msg: '',
        tipo: ''
      },
      error: {},
      canSubmit: true,
    };
  }

  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      case 404:
        console.log("error 404, archivo no encontrado")
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }



  //hacer que se ejecute una funcion de primeras
  componentDidMount() {
     this.getData(this.props.compra.id);
    this.props.btns(true);
  }
  async getData(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/listreferenceterms/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        this.setState({ datos: res.data });
        if (this.state.datos.length === 0) {
          this.props.CanNext(false);
        } else { this.props.CanNext(true); }
        this.listData();
      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  listData() {
    if (this.state.datos.length !== 0) {
      return <TerminosReferencia datos={this.state.datos}   modificar={this.modificar}  onEliminar={this.onEliminar} action={true} />;
    } else {
      return "";
    }
  }

  modificar = (id) => {
    this.getidreferenceterms(id)
  }
  async getidreferenceterms(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/referenceterms/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        this.setState({
          canSubmit: true,
          action:false, 
          id: res.data.id,
          Especificacion: res.data.Especificacion,
          observaciones: res.data.observaciones,
          cantidad: res.data.cantidad,
        });
      }
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  onSubmit = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'post',
      url: env.endpoint + '/referenceterms',
      data: {
        Especificacion: this.state.Especificacion,
        observaciones: this.state.observaciones,
        cantidad: this.state.cantidad,
       compra_id: this.props.compra.id,
      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }

    }).then(response => {
      this.setState({
        canSubmit: true,
        Especificacion: '',
        observaciones: '',
        cantidad: 0
      });
      this.componentDidMount()

      this.props.msgbn('Datos registrados correctamente.');

    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }

  onSubmitEditar = (id) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'PUT',
      url: env.endpoint + '/referenceterms/'+ this.state.id,
      data: {
        Especificacion: this.state.Especificacion,
        observaciones: this.state.observaciones,
        cantidad: this.state.cantidad,
       compra_id: this.props.compra.id,
      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }

    }).then(response => {

      this.setState({
        canSubmit: true,
        action:true, 
        id: '',
        Especificacion: '',
        observaciones: '',
        cantidad: 0
      });
      this.setState({
        canSubmit: true,
        action:true, 
        id: '',
        Especificacion: '',
        observaciones: '',
        cantidad: 0, 
        error:{}
      });
      this.componentDidMount()
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }
  

  onEliminar = (id) => {
    axios({
      method: 'DELETE',
      url: env.endpoint + '/referenceterms/' + id,
      headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    }).then(response => {
      this.componentDidMount()
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }

  syncField(ev, fieldName) { // data form
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
  }
  render() {
    return (
      <div>

        <Grid container spacing={24}>
          <Grid item xs={6} sm={3}>
            <TextField
              error={this.state.error.Especificacion ? true : false}
              label={this.state.error.Especificacion ? this.state.error.Especificacion[0] : "Especificacion"}
              placeholder="Especificacion"
              ref="nameCompra"
              name="nameCompra"
              onChange={(e) => this.syncField(e, 'Especificacion')}
              value={this.state.Especificacion}
              fullWidth={true}
              margin="normal"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              error={this.state.error.observaciones ? true : false}
              label={this.state.error.observaciones ? this.state.error.observaciones[0] : "Observaciones"}
              placeholder="Expecificar Observaciones"
              ref="observaciones"
              name="observaciones"
              onChange={(e) => this.syncField(e, 'observaciones')}
              value={this.state.observaciones}
              fullWidth={true}
              multiline
              margin="normal"
            />
          </Grid>
          <Grid item xs={3} sm={1}>
            <TextField
              error={this.state.error.cantidad ? true : false}
              label={this.state.error.cantidad ? this.state.error.cantidad[0] : "Cantidad"}
              placeholder="Cantidad"
              value={this.state.cantidad}
              ref="cantidad"
              name="cantidad"
              onChange={(e) => this.syncField(e, 'cantidad')}
              margin="normal"
            />
          </Grid>
        </Grid>

        {this.state.action ? <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit}> Registrar  </Button> :
          <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar}> Modificar  </Button>
        }

        {this.listData()}
      </div>

    )
  }
}

export default connect(mapSteToProps)(FormTerminosReferencia);