import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Icon from '@material-ui/core/Icon';

const styles = {
    tableWrapper: {
        overflowX: 'auto',
    },
};

export class ListComprasPendientes extends React.Component {


    getcompra(id){
        this.props.getcompraID(id);
    }
    getMapData() {

        if (this.props.datos) {
            return this.props.datos.map(row => {
                return (
                    <TableRow key={row.id}>
                        <TableCell > {row.NombreCompra} </TableCell>
                        <TableCell >{row.objetivo}</TableCell>
                        <TableCell >{row.updated_at}</TableCell>
                        <TableCell >
                             <Icon variant="contained"   color="primary" onClick={() => this.getcompra(row.id)} >build</Icon>    </TableCell>
                    </TableRow>
                );
            });
        }
        return "";
    }
    setData() {
        return (<Paper >
            <div style={styles.tableWrapper}>
                <Table  >
                    <TableHead>
                        <TableRow>
                            <TableCell>Nombre Compra</TableCell>
                            <TableCell >Objetivo</TableCell>
                            <TableCell >Fecha de Creacin</TableCell>
                            {this.props.action ? <TableCell >Acciones</TableCell> : <TableCell ></TableCell>}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {this.getMapData()}
                      
                    </TableBody>
                </Table>
            </div>
        </Paper>);
    }


    render() {
        return this.setData();
    }
}

export default ListComprasPendientes;