import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Icon from '@material-ui/core/Icon';
import DeleteIcon from '@material-ui/icons/Delete';

const styles = {
    tableWrapper: {
        overflowX: 'auto',
    },
};

export class LisPropuestaEconomica extends React.Component {

    
    modificar(id) {
        this.props.modificar(id);
    }

    eliminar(id) {
        this.props.onEliminar(id);
    }



    getMapData() {

        return this.props.datos.map(row => {
            return (
                <TableRow key={row.id}>
                    <TableCell component="th" scope="row">
                        {row.producto}
                    </TableCell>
                    <TableCell >{row.description}</TableCell>
                    <TableCell >{row.cantidad}</TableCell>
                    <TableCell >{row.precioSinImpusto}</TableCell>
                    <TableCell >{row.precioConImpuesto}</TableCell>
                    {this.props.action ? <TableCell >
                        <Icon color="primary" onClick={() => this.modificar(row.id)} >edit_icon</Icon>
                            <DeleteIcon color="error" onClick={() => this.eliminar(row.id)} />
                    </TableCell> : <TableCell > </TableCell>}
                </TableRow>
            );
        });
    }
    setData() {
        if (this.props.datos.length > 0) {
            return (
                  <div style={styles.tableWrapper}>
                <Table  >
                    <TableHead>
                        <TableRow>
                            <TableCell>Servicio o producto</TableCell>
                            <TableCell>Descripcin</TableCell>
                            <TableCell>Cantidad</TableCell>
                            <TableCell >$ valor con valor sin impuesto</TableCell>
                            <TableCell >$ valor con valor con impuesto</TableCell>
                            {this.props.action ? <TableCell >Acciones</TableCell> : <TableCell ></TableCell>}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {this.getMapData()}
                        <TableRow >
                            <TableCell rowSpan={3} />
                            <TableCell colSpan={3}>Total propuesta</TableCell>
                            <TableCell align="right">{
                                new Intl.NumberFormat('COL', {
                                    style: 'currency',
                                    currency: 'COL',
                                    minimumFractionDigits: 0,
                                    maximumFractionDigits: 0
                                }).format(this.props.datos.total)}</TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
                </div>
            );
        }
        return "";
    }
    render() {
        return(<div> { this.setData()} </div>)
    }
}
export default LisPropuestaEconomica;