import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Tooltip from '@material-ui/core/Tooltip';
import { connect } from 'react-redux';

const styles = {
    tableWrapper: {
        overflowX: 'auto',
    },
};




function mapSteToProps(state) {
    return { userAcceso: state.userAcceso , compra: state.compra }
}

export class ListPresupuestoafectado extends React.Component {
    
    componentWillReceiveProps() {
        this.props.actualizar();
    }
    
    
   
    getMapData() {
        if (this.props.datos) {
            return this.props.datos.data.map(row => {
                return (
                    <TableRow key={row.id}>
                        <TableCell >  {row.servicio}  </TableCell>
                        <TableCell >{row.componenteName}</TableCell>
                        <TableCell >{row.clusterName}</TableCell>
                        <TableCell >{row.operacionName}</TableCell>
                        <TableCell >{row.cantidad}</TableCell>
                        <TableCell  > 
                            <Tooltip disableFocusListener disableTouchListener title={row.porcentaje}>
                       <p> {
                            new Intl.NumberFormat('COL', {
                                style: 'currency',
                                currency: 'COL',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(row.valorPresupuesto)
                            }</p>
                            </Tooltip>
                        </TableCell>

                        <TableCell >{
                            new Intl.NumberFormat('COL', {
                                style: 'currency',
                                currency: 'COL',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(row.totalPago)
                        }</TableCell>
                    </TableRow>
                );
            });
        }
        return "";
    }
    setData() {
        return (<Paper >
            <div style={styles.tableWrapper}>
                <Table  >
                    <TableHead>
                        <TableRow>
                            <TableCell>Servicio</TableCell>
                            <TableCell >componente</TableCell>
                            <TableCell >cluster</TableCell>
                            <TableCell >Centro de operacin</TableCell>
                            <TableCell >Cantidad</TableCell>
                            <TableCell >$ valor presupuesto</TableCell>
                            <TableCell >$ Gasto</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {this.getMapData()}
                        <TableRow >
                            <TableCell rowSpan={4} />
                            <TableCell colSpan={4}>Totales</TableCell>
                            <TableCell align="right">{
                                new Intl.NumberFormat('COL', {
                                    style: 'currency',
                                    currency: 'COL',
                                    minimumFractionDigits: 0,
                                    maximumFractionDigits: 0
                                }).format(this.props.datos.TotalPresupuestado)}</TableCell>
                        
                        <TableCell align="right">{
                                new Intl.NumberFormat('COL', {
                                    style: 'currency',
                                    currency: 'COL',
                                    minimumFractionDigits: 0,
                                    maximumFractionDigits: 0
                                }).format(this.props.datos.TotalCompra)}</TableCell>
                        </TableRow>
                        <TableRow >
                            <TableCell rowSpan={4} />
                            <TableCell colSpan={3}>Ahorro</TableCell>
                            <TableCell align="right"></TableCell>
                        <TableCell align="right">{
                                new Intl.NumberFormat('COL', {
                                    style: 'currency',
                                    currency: 'COL',
                                    minimumFractionDigits: 0,
                                    maximumFractionDigits: 0
                                }).format(this.props.datos.TotalPresupuestado - this.props.datos.TotalCompra)}</TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
            </div>
        </Paper>);
    }


    render() {
        return this.setData();
    }
}

  
  export default  connect(mapSteToProps)(ListPresupuestoafectado);