import React from 'react';
import Card from '@material-ui/core/Card';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
//importamos las tablas 
import LisDetallePresupuesto from '../detallePresupuesto/lisdetallepresupuesto';
import TerminosReferencia from '../terminosReferencia';
import Entregables from '../entregables';
import env from "../../../config/env";
import axios from "axios";
import { store } from '../../../../store/store';
import ListPresupuestoafectado from '../../../presupuesto/list-presupuesto-afectado';
function mapSteToProps(state) {

  return {
    userAcceso: state.userAcceso
  }
}

class DetallesCompra extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      user: {},
      entregables: [],
      TerminosReferencia: [],
      presupuesto: { data: [] },
      presupuestoAfect: { data: [] },
      fechaSolicitud: '',
      FechaInicio:  '', 
      FechaFin: '' , 
    };
  }


  // validamos errores 
  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      case 404:
        console.log("error 404, archivo no encontrado")
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }

  componentDidMount() {
    //llamamos lo metodos que hay 
    this.getUser(this.props.COMPRADATA.user_id);
    this.getentregables(this.props.COMPRADATA.id);
    this.getTerminos(this.props.COMPRADATA.id);
    this.getPresupuesto(this.props.COMPRADATA.id);

    var dt = new Date(this.props.COMPRADATA.created_at).toLocaleDateString();
    this.setState({
      fechaSolicitud: dt,
      FechaInicio:  new Date(this.props.COMPRADATA.FechaInicio).toLocaleDateString(),
      FechaFin: new Date(this.props.COMPRADATA.FechaFin).toLocaleDateString(),
    })

    // var d = new Date('2019-01-16T22:00:36.000Z');
    //var dt = new Date('2019-01-16T22:00:36.000Z').toLocaleDateString()
    //console.log(dt);
    //console.log(d.toLocaleDateString());
    //console.log(d.getUTCHours()); // Hours
    //console.log(d.getUTCMinutes());
    //console.log(d.getUTCSeconds());




  }

  async getUser(id) {
    try {
      let res = await axios({
        ///users/iduser
        url: env.endpoint + '/users/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        if (res.data.length !== 0) {
          this.setState({
            user: res.data
          })
        } else {
          this.setState({
            estado: false,
          })
        }
      } else if (res.status === 202) {
        //console.log(res.data.error);
      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  async getentregables(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/listEntregables/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        this.setState({ entregables: res.data });
      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  async getTerminos(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/listreferenceterms/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        this.setState({ TerminosReferencia: res.data });
        if (this.state.datos.length === 0) {
          this.props.CanNext(false);
        } else { this.props.CanNext(true); }
        this.listData();
      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      //  this.getError(error.response);
    }
  }


  async getPresupuesto(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/ListdetallePresupuesto/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ presupuesto: res.data });

      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      this.getError(error.response);
    }
  }


  lispresupuestoAfect() {
    if (this.props.presupuestoAfect.data.length !== 0) {
      return <ListPresupuestoafectado datos={this.props.presupuestoAfect} />;
    } else {
      return "Error no tenemos presupuesto no se puede continuar...";
    }
  }

  listData() {
    if (this.state.presupuesto.data.length !== 0) {
      return <LisDetallePresupuesto datos={this.state.presupuesto} />;
    } else {
      return "Error no tenemos presupuesto no se puede continuar...";
    }
  }
  render() {
    return (
      <div>
        <Card>
          <p>1. Datos Solicitante</p>
        </Card>
        <Grid container spacing={24}>
          <Grid item xs={12} sm={6}>
            <p><b>Fecha de solicitud:   </b>
              {this.state.fechaSolicitud}

            </p>
            <p><b>Solicitante :</b> {this.state.user.name}   {this.state.user.apellido}
            </p>
            <p><b>Cargo:</b> {this.state.user.cargo}
            </p>
            <p><b>Direccin :</b> {this.state.user.direccion}
            </p>
          </Grid>
          <Grid item xs={12} sm={6}>
            <p><b>Identificacin:</b> {this.state.user.documento}
            </p>
            <p><b>E-mail:</b> {this.state.user.email}
            </p>
          </Grid>
        </Grid>
        <Card>
          <p>2. Datos General de compra</p>
        </Card>

        <Grid container spacing={24}>
          <Grid item xs={12} sm={12}>
            <p><b>Bien o Servicio a Contratar:</b> {this.props.COMPRADATA.NombreCompra}
            </p>
            <p><b>Objeto:</b> {this.props.COMPRADATA.objetivo}
            </p>
          </Grid>
          <Grid item xs={12} sm={6}>
            <p><b>Fecha de Inicio:</b> {this.state.FechaInicio}
            </p>
            <p><b>Fecha de Fin:</b> {this.state.FechaFin}
            </p>
          </Grid>
          <Grid item xs={12} sm={6}>
            <p><b>Plazo de ejecucin:</b> {this.state.FechaInicio - this.state.FechaFin}
            </p>
            <p><b>Das para el Inicio:</b> {this.props.COMPRADATA.FechaInicio}
            </p>
          </Grid>
        </Grid>


        {this.props.COMPRADATA.EstadoAction === 3 ? (
          <div> <Card><p>3. Presupuesto afectado.</p> </Card>
            {this.lispresupuestoAfect()} <br></br>
          </div>
        ) : (<div>
          <Card><p>3. Presupuesto Disponible.</p> </Card>
          {this.listData()} <br></br>
        </div>
          )}
        <Card>
          <p>4. Terminos de referencia</p>
        </Card>
        <TerminosReferencia datos={this.state.TerminosReferencia} action={false} />

        <Card>
          <p>5. Entragables</p>
        </Card>
        <Entregables datos={this.state.entregables} />
      </div>
    )
  }
}
export default connect(mapSteToProps)(DetallesCompra);