import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import SwipeableViews from 'react-swipeable-views';
import Paper from '@material-ui/core/Paper';

import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import FormProvedor from '../../Provedor/form-Provedor';
import FormBuscarProvedor from './form-buscar-provedor';

function TabContainer({ children }) {
  return (
    <div style={{ padding: 8 * 3 }}>
      {children}
    </div>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired
};

const styles = theme => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: 500,
  },
});

class PageProvedor extends React.Component {
  state = {
    value: 0,
  };

  
  componentDidMount() {
    this.props.siguienteUpdate(false);

}

  handleChange = (event, value) => {
    this.setState({ value });
  };

  handleChangeIndex = index => {
    this.setState({ value: index });
  };

  render() {
    const { theme } = this.props;
    return (
      <div >
        <Paper >
          <Tabs
            value={this.state.value}
            onChange={this.handleChange}
            indicatorColor="primary"
            textColor="primary"
            centered
          >
            <Tab label="Buscar" />
            <Tab label="Registrar" />
          </Tabs>
        </Paper>
        <SwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={this.state.value}
          onChangeIndex={this.handleChangeIndex}
        >
          <TabContainer >Buscar Provedor
            <FormBuscarProvedor
             father='propuesta'
             handleNext={this.props.handleNext} 
             provedor={this.props.provedor}
            />
          </TabContainer>
          <TabContainer > Registrar Provedor
             <FormProvedor father='propuesta'
              handleNext={this.props.handleNext} 
              provedor={this.props.provedor}
              />
            </TabContainer>
        </SwipeableViews>
      </div>
    );
  }
}

PageProvedor.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(PageProvedor);