import React, { Component } from 'react';
import Card from '@material-ui/core/Card';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import CircularProgress from '@material-ui/core/CircularProgress';
import FormTerminosReferenciaProvedor from '../politicas/form-terminos-referencia';
import FormEntregable from '../politicas/form-entregable';

//llamar la tabla que mustra los datos de la propuesta economica jjaja 
import LisPropuestaEconomica from './list-propuesta-economica';
import ListConfictos from '../../conflicto-interes/list-confictos';

import FormAceptar from './form-aceptar';
function mapSteToProps(state) {
    return { userAcceso: state.userAcceso }
}



class ShowPropuesta extends Component {

    constructor(props) {
        super(props);
        this.state = {
            anchoState: true,
            open: false,
        }
    }
    handleOpen = () => {
        this.setState({ open: true });
      };
    
      handleClose = () => {
        this.setState({ open: false });
      };

    updateStado = () => {
        this.setState({
            anchoState: !this.state.anchoState,
        });
    }

    modificarInicio = () => {
       this.props.inicio();
    }

    action() {
        if (this.props.propuesta.compra_id > 0) {
            return <FormTerminosReferenciaProvedor idCompra={this.props.propuesta.compra_id} />;
        } else {
            return  <CircularProgress  color="secondary" />;
        }
    }
    entregables() {
        if (this.props.propuesta.compra_id > 0) {
            return <FormEntregable idCompra={this.props.propuesta.compra_id} />;
        } else {
            return  <CircularProgress  color="secondary" />;
        }
    }
    render() {
        return (<div >
            <FormControlLabel
                control={
                    <Switch
                        checked={this.state.anchoState}
                        onChange={(e) => this.updateStado(e, 'rta')}
                        value='estado'
                    />
                }
            />
            <Grid container spacing={24}>
                <Grid item xs={12} sm={this.state.anchoState ? 12 : 6}>
                    <Card>
                        <p>1. Datos del proponente: </p>
                    </Card>
                    <p><b>Nombre Empresa:</b> {this.props.propuesta.nombre}  <b> NIT: </b> {this.props.propuesta.nit}
                    </p>
                    <p><b>Representante Legal:</b> {this.props.propuesta.representate}  <b> DI:</b> {this.props.propuesta.DI}
                    </p>
                    <p><b>Telfono: </b> {this.props.propuesta.telefono}  <b> Direccin:</b> {this.props.propuesta.direccion}
                    </p>
                    <p><b>E-mail de contacto:</b> {this.props.propuesta.email}  <b> Ciudad: </b> {this.props.propuesta.ciudad}
                    </p>

                    <Card>
                        <p>2. Conflictos de Inters</p>
                    </Card>
                   < ListConfictos propuesta={this.props.propuesta}   />
                    <br></br>
                    <Card>
                        <p>3. Evaluacin Tcnica</p>
                    </Card>
                    <Card>
                        {this.action() // compra no estan aceptados jajajj 
                        }
                    </Card>
                    <Card>
                        <p>4. Productos</p>
                        {this.entregables() //son los entregables lo cuales son de la compra no estan aceptados jajajj 
                        }
                    </Card>
                </Grid>
                <Grid item xs={12} sm={this.state.anchoState ? 12 : 6}>
                    <Card>
                    <Grid container spacing={24}>
                            <Grid item xs={12} sm={9}>
                                <p>5. Propuesta Econmica Presentada </p>
                                </Grid>
                        <Grid item xs={3} sm={3}>
                        {this.props.compra.EstadoAction === 2 ||this.props.compra.EstadoAction === 4?
          <FormAceptar   compra={this.props.compra} propuesta={this.props.propuesta} inicio={this.modificarInicio} /> : this.props.propuesta.estado===1? 'Propuesta aceptada':'Esta propuesta no fue aceptada '}
                        </Grid>
                    </Grid>
                    </Card>
                 <LisPropuestaEconomica data={this.props.propuesta.id} datos={this.props.propuestaeconomica} action={false} />
                </Grid>
            </Grid>
        </div>);
    }
}
export default connect(mapSteToProps)(ShowPropuesta);
