import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Icon from '@material-ui/core/Icon';
import { connect } from 'react-redux';
import MSGEstado from '../../shared/msg-estado';
import env from "../../../components/config/env";
import axios from "axios";
import { store } from '../../../store/store';

import Pagination from "react-js-pagination"; 


const styles = {
    tableWrapper: {
        overflowX: 'auto',
    },
};

function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}
export class PageCompraLists extends React.Component {


  constructor(props) {
    super(props);
    this.state= {
    value: 0,
    user: {},
    dataCompra:{data:[]},
    activePage: 1 ,
    totalItemsCount : 0, 
    
   }
  }

  handlePageChange = (pageNumber) => {
    //console.log(`active page is ${pageNumber}`);
    this.setState( {activePage: pageNumber}
      );
      this.getComprasFull(pageNumber);
  }


  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      case 404:
        console.log("error 404, archivo no encontrado")
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }

  componentDidMount() {
    this.getComprasFull(1);
  }


  async getComprasPersonTipe(id, Tipo) { // Obtener los datos
    try {
      let res = await axios({
        url: env.endpoint + '/compras/' + id  + '/' + Tipo,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
   this.setState({
     dataCompra: res.data
   })
    
    } catch (error) {
      this.getError(error.response);
    }
  }


  async getComprasFull(page) { // Obtener los datos de full compras
    try {
      let res = await axios({
        url: env.endpoint + '/compras/?page=' + page ,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
   this.setState({
     dataCompra: res.data,
     totalItemsCount: res.data.total
   })
    
    } catch (error) {
      this.getError(error.response);
    }
  }


  compraLink(id){  
    this.props.history.push('/propuesta/'+ id);
  }

    
    getMapData() {

        if (this.state.dataCompra.data.length > 0) {
            return this.state.dataCompra.data.map(row => {
                return (
                    <TableRow key={row.id}>
                    <TableCell > {row.id} </TableCell>
                        <TableCell > {row.NombreCompra} </TableCell>
                        <TableCell >{row.objetivo}</TableCell>
                        <TableCell >{row.updated_at}</TableCell>
                        <TableCell >
                        <MSGEstado estado={row.EstadoAction} /></TableCell>
                        <TableCell >
                        <Icon variant="contained"   color="primary" onClick={() => this.compraLink(row.id)} >visibility</Icon>    </TableCell>

                    </TableRow>
                );
            });
        }
        return "";
    }
    setData() {
        return (<Paper >
            <div style={styles.tableWrapper}>
                <Table  >
                    <TableHead>
                        <TableRow>
                        <TableCell>N Compra</TableCell>
                            <TableCell>Nombre Compra</TableCell>
                            <TableCell >Objetivo</TableCell>
                            <TableCell >Fecha de Creacin</TableCell>
                            <TableCell >Estado</TableCell> 
                            <TableCell >Acciones</TableCell> 
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {this.getMapData()}
                    </TableBody>
                </Table>


                {this.state.dataCompra.data.length > 0? 
         <Pagination
          activePage={this.state.activePage}
          itemsCountPerPage={10}
          totalItemsCount={this.state.totalItemsCount}
          pageRangeDisplayed={5}
          onChange={this.handlePageChange}
        />: '' }
      </div>
        </Paper>);
    }


    render() {
        return this.setData();
    }
}



export default connect(mapSteToProps)(PageCompraLists);