import React from 'react';
import env from "../../../config/env";
import axios from "axios";
import { connect } from 'react-redux';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import TerminosReferenciaAceptar from './terminos-Referencia-aceptar';
import CircularProgress from '@material-ui/core/CircularProgress';
import Reference from './view/reference';

function mapSteToProps(state) {
  return { User: state.UserData, userAcceso: state.userAcceso }
}


const Styles = ({

  cont: {
    display: 'flex',
    justifyContent: 'center'
  },
  tableWrapper: {
    overflowX: 'auto',
},
});


class FormTerminosReferenciaProvedor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      datos: [],
      action: true,
      msg: {
        estado: false,
        msg: '',
        tipo: ''
      },
      canSubmit: true,
    };
  }

  componentDidMount() {
    if (this.props.idCompra != null) {
      this.getData(this.props.idCompra);
    }
    //  this.props.btns(true);
  }

  async getData(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/listreferenceterms/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        this.setState({ datos: res.data });
        this.listData();
      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      let errorObject = JSON.parse(JSON.stringify(error));
      console.log(errorObject);
    }
  }


  mapdata() {
    if (this.state.datos.length > 0) {
      if(this.props.form){
        return this.state.datos.map((row , index) => {
          return (<TerminosReferenciaAceptar key={index}  provedor={this.props.provedor} datos={row} />);
        });
      }else{
        return this.state.datos.map((row , index) => {
          return (<Reference key={index} provedor={this.props.provedor} datos={row} />);
        });
      }
     
    } else {
      return <div style={Styles.cont} >   <CircularProgress color="primary" />  </div>;
    }
  }

  listData() {
    return (<Paper>
       <div style={Styles.tableWrapper}>
      <Table >
        <TableHead>
          <TableRow>
            <TableCell>Especificacin </TableCell>
            <TableCell>Observaciones </TableCell>
            <TableCell numeric>Cantidad</TableCell>
            <TableCell >Observaciones</TableCell>
            <TableCell >Cumple</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {this.mapdata()}
        </TableBody>
      </Table>
      </div>
    </Paper>);
  }
  syncField(ev, fieldName) { // data form
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
  }
  render() {
    return (
      <div>
        {this.listData()}
      </div>
    )
  }
}
export default connect(mapSteToProps)(FormTerminosReferenciaProvedor);