import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Icon from '@material-ui/core/Icon';
import Pagination from "react-js-pagination"; 


const styles = {
    tableWrapper: {
        overflowX: 'auto',
    },
};

export class ListPresupuesto extends React.Component {

    modificar(id) {
        this.props.modificar(id);
    }

    eliminar(id) {
        this.props.onEliminar(id);
    }
    getMapData() {
        if (this.props.datos) {
            return this.props.datos.data.map(row => {
                return (
                    <TableRow key={row.id}>
                      <TableCell >{row.clusterName}</TableCell>
                        <TableCell >{row.componenteName}</TableCell>
                        <TableCell >{row.proyectoName}</TableCell>
                        <TableCell >{row.operationName}</TableCell>
                        <TableCell >
                        {
                            new Intl.NumberFormat('COL', {
                                style: 'currency',
                                currency: 'COL',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(row.totalPresupuesto)
                        }
                      </TableCell>
                        <TableCell >
                        {
                            new Intl.NumberFormat('COL', {
                                style: 'currency',
                                currency: 'COL',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(row.TotalGasto)
                        }
                        </TableCell>
                        <TableCell >
                        
                        {
                            new Intl.NumberFormat('COL', {
                                style: 'currency',
                                currency: 'COL',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(row.comprometido)
                        }
            </TableCell>
                        {this.props.action ? <TableCell >
                            <Icon color="primary" onClick={() => this.modificar(row.id)} >edit_icon</Icon>
                        </TableCell> : <TableCell ></TableCell>}
                    </TableRow>
                );
            });
        }
        return "";
    }

    
    setData() {
        return (<Paper >
            <div style={styles.tableWrapper}>
                <Table  >
                    <TableHead>
                        <TableRow>
                            <TableCell >Cluster</TableCell>
                            <TableCell >Componente</TableCell>
                            <TableCell >Proyecto</TableCell>
                            <TableCell >Centro de operacin</TableCell>
                            <TableCell >$ Presupuesto</TableCell>
                            <TableCell >$ Gasto</TableCell>
                            <TableCell >$ Comprometido</TableCell>
                            {this.props.action ? <TableCell >Acciones</TableCell> : <TableCell ></TableCell>}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {this.getMapData()}
                        <TableRow >
                            <TableCell rowSpan={3} />
                            <TableCell colSpan={3}>Total</TableCell>
                            <TableCell align="right">
                            
                               
                        {
                            new Intl.NumberFormat('COL', {
                                style: 'currency',
                                currency: 'COL',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(this.props.datos.totalPresupuesto)
                        }
                          </TableCell>
                        </TableRow>
                    </TableBody>

                </Table>
                {this.props.datos.data.length > 0? 
         <Pagination
          activePage={this.props.activePage}
          itemsCountPerPage={10}
          totalItemsCount={this.props.datos.totalData}
          pageRangeDisplayed={5}
          onChange={this.props.handlePageChange}
        />: '' }
            </div>
        </Paper>);
    }
    render() {
        return this.setData();
    }
}

export default ListPresupuesto;