import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import SwipeableViews from 'react-swipeable-views';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import FormPresupuesto from './form-presupuesto';
import FormPeriodo from '../periodo/form-periodo';
import { connect } from 'react-redux';

function mapSteToProps(state) {
  return { User: state.UserData, userAcceso: state.userAcceso }
}


function TabContainer({ children }) {
  return (
    <div style={{ padding: 8 * 3 }}>
      {children}
    </div>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired
};

const styles = theme => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: 500,
  },
});

class PagePresupuesto extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      compra: {},
      value: 0,
    }
  };


  componentDidMount() {
      
  }
  handleChange = (event, value) => {
    this.setState({ value });
  };
  handleChangeIndex = index => {
    this.setState({ value: index });
  };
  render() {
    const { theme } = this.props;
    return (
      <div >
        <Paper >
          <Tabs
            value={this.state.value}
            onChange={this.handleChange}
            indicatorColor="primary"
            textColor="primary"
            centered
          >
            <Tab label="Presupuesto" />
            <Tab label="Periodos" />
            
          </Tabs>
        </Paper>
        <SwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={this.state.value}
          onChangeIndex={this.handleChangeIndex}
        >
          <TabContainer >
            <FormPresupuesto />
          </TabContainer>
          <TabContainer >
          <FormPeriodo />
          </TabContainer>
        </SwipeableViews>
      </div>
    );
  }
}
PagePresupuesto.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default connect(mapSteToProps)(withStyles(styles, { withTheme: true })(PagePresupuesto));