import React from 'react';

import TextField from '@material-ui/core/TextField';
import env from "../config/env";
import axios from "axios";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import { store } from '../../store/store';


import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormLabel from '@material-ui/core/FormLabel';
import ListUserData from './list-data';

function mapSteToProps(state) {
    return { userAcceso: state.userAcceso }
}
class FormUser extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            value: 'User',
            name: '',
            apellido: '',
            email: '',
            rol: '',
            cargo: '',
            direccion: '',
            documento: '',
            error: {},
            activePage:  1, 
            user:{data: {}}, 
            msg: {
                estado: false,
                msg: '',
                tipo: ''
            },
            canSubmit: true,
            action: true,
        };
    }


    componentDidMount() {
    this.getdataUser(1);
    
    }
    
    syncField(ev, fieldName) {
        let element = ev.target;
        let value = element.value;
        let jsonState = {};
        jsonState[fieldName] = value;
        this.setState(jsonState);
    }

    getError(response) {
        switch (response.status) {
            case 401:
                store.dispatch({
                    type: 'SET_USER_ACCESO',
                    payload: {
                        userAcceso: ''
                    }
                })
                break;
            case 422:
                this.setState({
                    error: response.data
                })
                break;
            case 404:
                console.log("error 404, archivo no encontrado")
                break;
            default:
                console.log('error inesperado')
                break;
        }
    }

    async getdataUser(page) {
        try {
          let res = await axios({
            url: env.endpoint + '/userData?page='+ page,
            method: 'get',
            timeout: 8000,
            headers: {
              'Accept': '*/*',
              'Authorization': this.props.userAcceso
            }
          })
          if (res.status === 200) {
            // peticion correctamente
            this.setState({ user: res.data });
          }
        }
        catch (error) {
         // this.getError(error.response);
        }
      }
    


    onSubmit = (e) => {
        this.setState({ canSubmit: false });
        axios({
            method: 'post',
            url: env.endpoint + '/users',
            data: {
                name: this.state.name,
                apellido: this.state.apellido,
                email: this.state.email,
                rol: this.state.value,
                cargo: this.state.cargo,
                direccion: this.state.direccion,
                documento: this.state.documento,
            }, headers: {
                'Accept': '*/*',
                'Authorization': this.props.userAcceso
            }

        }).then(response => {
            this.clear();

        })
            .catch(error => {
                this.setState({ canSubmit: true });
                this.getError(error.response);
            });
    }



    handleChange = event => {
        this.setState({ value: event.target.value });
    };


    handlePageChange = (pageNumber) => {
        //console.log(`active page is ${pageNumber}`);
        this.setState( {activePage: pageNumber}
          );
          this.getdataUser(pageNumber);
      }



      modificar = (id) => {
        this.clear();
        this.getUser(id);
      }
      
      async getUser(id) { // Obtener los datos para modificar 
        try {
          let res = await axios({
            url: env.endpoint + '/users/' + id,
            method: 'get',
            timeout: 8000,
            headers: {
              'Accept': '*/*',
              'Authorization': this.props.userAcceso
            }
          })
          if (res.status === 200) {
            this.setState({
              error:{}, 
              action: false, 
              id: res.data.id, 
              name: res.data.name,
              apellido: res.data.apellido,
              email: res.data.email,
              value: res.data.rol,
              cargo: res.data.cargo,
              direccion: res.data.direccion,
              documento: res.data.documento,
            })
          }
        } catch (error) {
          this.getError(error.response);
        }
      }
      
      
      
      onSubmitEditar = (e) => {
        this.setState({ canSubmit: false });
        axios({
          method: 'put',
          url: env.endpoint + '/users/'+ this.state.id,
          data: {
            name: this.state.name,
            apellido: this.state.apellido,
            email: this.state.email,
            rol: this.state.value,
            cargo: this.state.cargo,
            direccion: this.state.direccion,
            documento: this.state.documento,
          }, headers: {
            'Accept': '*/*',
            'Authorization': this.props.userAcceso
          }
      
        }).then(response => {
          this.clear();
        })
          .catch(error => {
            this.setState({ canSubmit: true });
           this.getError(error.response);
          });
      }
      





    clear() {
        this.setState({
            action:true, 
            canSubmit: true, 
            value: 'User',
            name: '',
            apellido: '',
            email: '',
            rol: '',
            cargo: '',
            direccion: '',
            documento: '',
            error: {}
        })
        this.getdataUser(1)
    }
    render() {
        return (<div >
            <Grid container spacing={24}>
                <Grid item xs={12} sm={6}>
                    <TextField
                        error={this.state.error.name ? true : false}
                        label={this.state.error.name ? this.state.error.name[0] : "Nombre"}
                        value={this.state.name}
                        placeholder="Nombre Empresa"
                        ref="NombreEmpresa"
                        name="NombreEmpresa"
                        onChange={(e) => this.syncField(e, 'name')}
                        fullWidth={true}
                        margin="normal"
                    />
                    <TextField
                        error={this.state.error.apellido ? true : false}
                        label={this.state.error.apellido ? this.state.error.apellido[0] : "Apellido"}
                        placeholder="Apellido"
                        value={this.state.apellido}
                        ref="apellido"
                        name="apellido"
                        onChange={(e) => this.syncField(e, 'apellido')}
                        fullWidth={true}
                        margin="normal"
                    />
                    <TextField
                        error={this.state.error.email ? true : false}
                        label={this.state.error.email ? this.state.error.email[0] : "Email"}
                        placeholder="Email example@dividendoporcolombia.org"
                        value={this.state.email}
                        ref="email"
                        name="email"
                        onChange={(e) => this.syncField(e, 'email')}
                        fullWidth={true}
                        margin="normal"
                    />
                </Grid>
                <Grid item xs={12} sm={4}>
                    <TextField
                        error={this.state.error.documento ? true : false}
                        label={this.state.error.documento ? this.state.error.documento[0] : "Documento"}
                        value={this.state.documento}
                        placeholder="Documento"
                        ref="documento"
                        name="documento"
                        onChange={(e) => this.syncField(e, 'documento')}
                        fullWidth={true}
                        margin="normal"
                    />
                    <TextField
                        error={this.state.error.cargo ? true : false}
                        label={this.state.error.cargo ? this.state.error.cargo[0] : "Cargo"}
                        placeholder="cargo"
                        value={this.state.cargo}
                        ref="direccion"
                        name="direccion"
                        onChange={(e) => this.syncField(e, 'cargo')}
                        fullWidth={true}
                        margin="normal"
                    />
                    <TextField
                        error={this.state.error.direccion ? true : false}
                        label={this.state.error.direccion ? this.state.error.direccion[0] : "Direccin"}
                        placeholder="Direccion"
                        value={this.state.direccion}
                        ref="ciudad"
                        name="ciudad"
                        onChange={(e) => this.syncField(e, 'direccion')}
                        fullWidth={true}
                        margin="normal"
                    />

                </Grid>
                <Grid item xs={12} sm={2}>
                    <FormLabel component="legend">Rol</FormLabel>
                    <RadioGroup
                        //aria-label="gender"
                        // name="gender2"
                        //className={classes.group}
                        value={this.state.value}
                        onChange={this.handleChange}
                    >
                        <FormControlLabel
                            value="User"
                            control={<Radio color="primary" />}
                            label="User"
                            labelPlacement="start"
                        />
                        <FormControlLabel
                            value="Colaborador"
                            control={<Radio color="primary" />}
                            label="Colaborador"
                            labelPlacement="start"
                        />
                        <FormControlLabel
                            value="Super-Admin"
                            control={<Radio color="primary" />}
                            label="Super-Admin"
                            labelPlacement="start"
                        />
                    </RadioGroup>
                </Grid>
            </Grid>
            {this.state.action ? <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit}> Registrar  </Button> :
                <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar}> Modificar  </Button>
            }

            <ListUserData user={this.state.user} handlePageChange={this.handlePageChange} activePage={this.state.activePage}  modificar={this.modificar} />
        </div>
        );
    }
}
export default connect(mapSteToProps)(FormUser);