import React from 'react';
import env from "../config/env";
import axios from "axios";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import { store } from '../../store/store';
import Chart from './BarChart';
import CircularProgress from '@material-ui/core/CircularProgress';


const Styles = ({
    cont: {
        display: 'flex',
        justifyContent: 'center'
    },

});

function mapSteToProps(state) {
    return { userAcceso: state.userAcceso }
}

class Buscador extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            chartData: {},
            cluster_id: '',
            componente_id: '',
            operacion_id: '',
            proyecto_id: '',
            componentes: [],
            operacions: [],
            Cluster: [],
            Proyectos: [],
            error: {},
            mostrar: false,
            message: false,
            msg: '',
            canSubmit: true,
            action: true,
            labels: ['Presupuesto', 'Comprometido', 'gasto', 'Disponible'],
            datasets: [],
        }
    }




    componentWillMount() {
        this.getcluster();
        this.getComponente();
        this.getOperacion();
        this.getProyectos();
        // this.getChartData();
    }


    syncField(ev, fieldName) { // data form

        let element = ev.target;
        let value = element.value;
        let jsonState = {};
        jsonState[fieldName] = value;
        this.setState(jsonState);
    }


    getError(response) {
        switch (response.status) {
            case 401:
                store.dispatch({
                    type: 'SET_USER_ACCESO',
                    payload: {
                        userAcceso: ''
                    }
                })
                break;
            case 422:
                this.setState({
                    error: response.data
                })
                break;
            case 404:
                console.log("error 404, archivo no encontrado")
                break;
            default:
                console.log('error inesperado')
                break;
        }
    }




    async getcluster() {
        try {
            let res = await axios({
                url: env.endpoint + '/clusters/',
                method: 'get',
                //  timeout: 8000,
                headers: {
                    'Accept': '*/*',
                    'Authorization': this.props.userAcceso
                }
            })
            if (res.status === 200) {
                // peticion correctamente
                this.setState({ Cluster: res.data });
            }
        }
        catch (error) {
            this.getError(error.response);
        }
    }

    async getComponente() {
        try {
            let res = await axios({
                url: env.endpoint + '/componentes/',
                method: 'get',
                //  timeout: 8000,
                headers: {
                    'Accept': '*/*',
                    'Authorization': this.props.userAcceso
                }
            })
            if (res.status === 200) {
                // peticion correctamente
                this.setState({ componentes: res.data });
            }
        }
        catch (error) {
            this.getError(error.response);
        }
    }


    async getOperacion() {
        try {
            let res = await axios({
                url: env.endpoint + '/operacions/',
                method: 'get',
                //  timeout: 8000,
                headers: {
                    'Accept': '*/*',
                    'Authorization': this.props.userAcceso
                }
            })
            if (res.status === 200) {
                // peticion correctamente
                this.setState({ operacions: res.data });
            }
        }
        catch (error) {
            this.getError(error.response);
        }
    }
    listCluster() {
        return this.state.Cluster.map(row => {
            return (
                <MenuItem key={row.id} value={row.id}>{row.nombre}</MenuItem>
            );
        });
    }

    listComponente() {
        return this.state.componentes.map(row => {
            return (
                <MenuItem key={row.id} value={row.id}>{row.nombre}</MenuItem>
            );
        });
    }
    listOperation() {
        return this.state.operacions.map(row => {
            return (
                <MenuItem key={row.id} value={row.id}>{row.nombre}</MenuItem>
            );
        });
    }
    listProyecto() {
        return this.state.Proyectos.map(row => {
            return (
                <MenuItem key={row.id} value={row.id}>{row.nombre}</MenuItem>
            );
        });
    }
    async getProyectos() {
        try {
            let res = await axios({
                url: env.endpoint + '/proyectos/',
                method: 'get',
                //  timeout: 8000,
                headers: {
                    'Accept': '*/*',
                    'Authorization': this.props.userAcceso
                }
            })
            if (res.status === 200) {

                // peticion correctamente
                this.setState({ Proyectos: res.data });
            }
        }
        catch (error) {
            this.getError(error.response);

        }
    }

    
    async data() {
        this.setState({ action: false })
        try {
            let res = await axios({
                url: env.endpoint + '/presupuestoData/?cluster_id=' + this.state.cluster_id + '&componente_id=' + this.state.componente_id + '&operacion_id=' + this.state.operacion_id + '&proyecto_id=' + this.state.proyecto_id + '&periodo_id=1',
                method: 'get',
                //  timeout: 8000,
                headers: {
                    'Accept': '*/*',
                    'Authorization': this.props.userAcceso
                }
            })
            if (res.status === 200) {
                this.setState({
                    titulo: 'Dividendo por colombia',
                    labels: ['Presupuesto  '
                        + res.data.TotalPresupuesto,
                    'Comprometido ' + res.data.comprometido,
                    'Gasto ' + res.data.gasto,
                    'Disponible ' + res.data.disponible],
                    datasets: [
                        {
                            label: 'Dinero',
                            data: [res.data.TotalPresupuesto, res.data.comprometido, res.data.gasto, res.data.disponible],
                            backgroundColor: [
                                'rgba(54, 162, 235, 0.6)',
                                'rgba(75, 192, 192, 0.6)',
                                'rgba(255, 99, 132, 0.6)',
                                'rgba(255, 206, 86, 0.6)',
                                'rgba(153, 102, 255, 0.6)',
                                'rgba(255, 159, 64, 0.6)',
                                'rgba(255, 99, 132, 0.6)'
                            ]
                        }],
                })
            }
            this.setState({ action: true })
            if (res.status === 200) {

                this.setState({
                    mostrar: true,
                    message: false,
                    msg: '',
                })
            } else if (res.status === 203) {
                this.setState({
                    mostrar: false,
                    message: true,
                    msg: res.data.Error,
                });
            }
        }
        catch (error) {
            this.setState({ action: true, mostrar: false })
            this.getError(error.response);
        }
    }

    render() {
        return (
            <div>
                <Grid container spacing={24}>
                    {this.state.message ? <p style={{ color: 'red' }}> {this.state.msg}</p> : ''
                    }
                </Grid>
                <Grid container spacing={24}>

                    <Grid item xs={12} sm={6}>
                        <InputLabel htmlFor="Cluster">{this.state.error.cluster ? this.state.error.cluster[0] : "Cluster"}</InputLabel>
                        <Select
                            error={this.state.error.cluster ? true : false}
                            fullWidth={true}
                            value={this.state.cluster_id}
                            onChange={(e) => this.syncField(e, 'cluster_id')}
                            inputProps={{
                                name: 'Cluster',
                                id: 'Cluster',
                            }}
                        >
                            {this.listCluster()}
                        </Select>
                        <InputLabel htmlFor="Componente">{this.state.error.componente ? this.state.error.componente[0] : "componente"}</InputLabel>
                        <Select
                            error={this.state.error.componente ? true : false}
                            fullWidth={true}
                            value={this.state.componente_id}
                            onChange={(e) => this.syncField(e, 'componente_id')}
                            inputProps={{
                                name: 'Componente',
                                id: 'Componente',
                            }}
                        >
                            {this.listComponente()}
                        </Select>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <InputLabel htmlFor="operacion">{this.state.error.operacion ? this.state.error.operacion[0] : "Centro de operacin"}</InputLabel>
                        <Select
                            error={this.state.error.operacion ? true : false}
                            fullWidth={true}
                            value={this.state.operacion_id}
                            onChange={(e) => this.syncField(e, 'operacion_id')}
                            inputProps={{
                                name: 'operacion',
                                id: 'operacion',
                            }}
                        >
                            {this.listOperation()}
                        </Select>
                        <InputLabel htmlFor="proyecto">{this.state.error.proyecto ? this.state.error.proyecto[0] : "proyecto"}</InputLabel>
                        <Select
                            error={this.state.error.proyecto ? true : false}
                            fullWidth={true}
                            value={this.state.proyecto_id}
                            onChange={(e) => this.syncField(e, 'proyecto_id')}
                            inputProps={{
                                name: 'proyecto',
                                id: 'proyecto',
                            }}
                        >
                            {this.listProyecto()}
                        </Select>
                    </Grid>
                </Grid>
                <Button variant="contained" disabled={!this.state.canSubmit} color="primary" onClick={(e) => this.data()}> Generar reporte </Button>
                {this.state.mostrar ?
                    this.state.action ?
                        <Chart chartData={{ labels: this.state.labels, datasets: this.state.datasets }} location={this.state.titulo} legendPosition="bottom" />
                        : <div style={Styles.cont} > <CircularProgress color="secondary" /></div> : ''
                }
            </div>
        );
    }
}

export default connect(mapSteToProps)(Buscador);