import React from 'react';

import TextField from '@material-ui/core/TextField';
import env from "../../../config/env";
import axios from "axios";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import NumberFormat from 'react-number-format';
import { store } from '../../../../store/store';

//llamar la tabla que mustra los datos jsjs 
import LisPropuestaEconomica from './list-propuesta-economica';

function mapSteToProps(state) {
    return { userAcceso: state.userAcceso }
}
/**
 * listar
 * create y modificar
 * 
 */



function NumberFormatCustom(props) {
    const { inputRef, onChange, ...other } = props;

    return (
        <NumberFormat
            {...other}
            getInputRef={inputRef}
            onValueChange={values => {
                onChange({
                    target: {
                        value: values.value,
                    },
                });
            }}
            thousandSeparator
            prefix="$"
        />
    );
}

NumberFormatCustom.propTypes = {
    inputRef: PropTypes.func.isRequired,
    onChange: PropTypes.func.isRequired,
};
class FormPropuestaEconomica extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            id: '',
            producto: '',
            Cantidad: 0,
            descripcion: '',
            precioSinImpuesto: '',
            PrecioComImpuesto: '',
            propuestaeconomica: [],
            error:{},
            msg: {
                estado: false,
                msg: '',
                tipo: ''
            },
            canSubmit: true,
            action: true,
        };
    }


    syncField(ev, fieldName) {
        let element = ev.target;
        let value = element.value;
        let jsonState = {};
        jsonState[fieldName] = value;
        this.setState(jsonState);
    }


    componentDidMount() {
        if (this.props.propuesta.id > 0) {
            this.getDatos(this.props.propuesta.id);
        }
    }


    // ver problemas de seguridad

    getError(response) {
        switch (response.status) {
            case 401:
                store.dispatch({
                    type: 'SET_USER_ACCESO',
                    payload: {
                        userAcceso: ''
                    }
                })
                break;
            case 422:
                this.setState({
                    error: response.data
                })
                break;
            case 404:
                console.log("error 404, archivo no encontrado")
                break;
            default:
                console.log('error inesperado')
                break;
        }
    }





    async getDatos(id) {
        try {
            let res = await axios({
                ///propuestaCliente/idProposal
                url: env.endpoint + '/propuestaCliente/' + id,
                method: 'get',
                timeout: 8000,
                headers: {
                    'Accept': '*/*',
                    'Authorization': this.props.userAcceso
                }
            })
            if (res.status === 200) {
                // peticion correctamente
                //console.log(res.data);

                if(res.data.length > 0){
                    this.props.siguienteUpdate(true);
                }else{
                    this.props.siguienteUpdate(false);
                }
                this.setState({
                    propuestaeconomica: res.data
                })

            } else if (res.status === 202) {
                //console.log(res.data.error);
            }
            // Don't forget to return something   
            return res.data
        }
        catch (error) {
            this.getError(error);
        }
    }

///

modificar = (id) => {
    this.getDetallePropuesta(id)
  }
  async getDetallePropuesta(id) { // Obtener los datos para modificar 
    try {
      let res = await axios({
        url: env.endpoint + '/propuestaeconomicas/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        this.setState({
          action: false,
          id: res.data.id,
          producto: res.data.producto,
          cantidad: res.data.cantidad,
          description: res.data.description,
          precioSinImpuesto: res.data.precioSinImpusto,
          precioConImpuesto: res.data.precioConImpuesto,
        })
      }
    } catch (error) {
      this.getError(error.response);
    }
  }

    onSubmit = (e) => {
        this.setState({ canSubmit: false });
        axios({
            method: 'POST',
            url: env.endpoint + '/propuestaeconomicas',
            data: {
                producto: this.state.producto,
                cantidad: this.state.cantidad,
                description: this.state.description,
                precioSinImpusto: this.state.precioSinImpuesto,
                precioConImpuesto: this.state.precioConImpuesto,
                proposal_id: this.props.propuesta.id
            },
            headers: {
                'Accept': '*/*',
                'Authorization': this.props.userAcceso
            }
        }).then(response => {
            this.getDatos(this.props.propuesta.id);
            this.clear();
        })
            .catch(error => {
                this.setState({ canSubmit: true });
                this.getError(error.response);
            });
    }




    onSubmitEditar = (e) => {
        this.setState({ canSubmit: false });
        axios({
            method: 'put',
            url: env.endpoint + '/propuestaeconomicas/' + this.state.id,
            data: {
                producto: this.state.producto,
                cantidad: this.state.cantidad,
                description: this.state.description,
                precioSinImpusto: this.state.precioSinImpuesto,
                precioConImpuesto: this.state.precioConImpuesto,
            },
            headers: {
                'Accept': '*/*',
                'Authorization': this.props.userAcceso
            }
        }).then(response => {
            //console.log(response.data);
            this.getDatos(this.props.propuesta.id);
            this.clear();
        })
            .catch(error => {
                this.setState({ canSubmit: true });
                this.getError(error.response);
            });
    }


    onEliminar = (id) => {
        axios({
          method: 'DELETE',
          url: env.endpoint + '/propuestaeconomicas/' + id,
          headers: {
            'Accept': '*/*',
            'Authorization': this.props.userAcceso
          }
        }).then(response => {
            this.getDatos(this.props.propuesta.id);
        })
          .catch(error => {
            this.setState({ canSubmit: true });
            this.getError(error.response);
          });
      }

    clear() {
        this.setState({
            canSubmit: true,
            action:true, 
            id: '',
            producto: '',
            cantidad: '',
            description: '',
            precioSinImpuesto: '',
            precioConImpuesto: '',
            proposal_id: '',
            error:{}
        });
    }



    render() {
        return (<div >
            <Grid container spacing={24}>
                <Grid item xs={12} sm={6}>
                    <TextField
                        error={this.state.error.producto ? true : false}
                        label={this.state.error.producto ? this.state.error.producto[0] : "Producto"}
                        value={this.state.producto}
                        placeholder="Ingrese producto o servicio "
                        ref="producto"
                        name="producto"
                        onChange={(e) => this.syncField(e, 'producto')}
                        fullWidth={true}
                        margin="normal"
                    />
                    <TextField
                        error={this.state.error.description ? true : false}
                        label={this.state.error.description ? this.state.error.description[0] : "Descripcin"}
                        value={this.state.description}
                        placeholder="Descripcin del producto"
                        ref="description"
                        name="description"
                        onChange={(e) => this.syncField(e, 'description')}
                        fullWidth={true}
                        margin="normal"
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        error={this.state.error.precioSinImpusto ? true : false}
                        label={this.state.error.precioSinImpusto ? this.state.error.precioSinImpusto[0] : "Precio sin Iva"}
                        value={this.state.precioSinImpuesto}
                        placeholder="Precio sin impuestos "
                        ref="precioSinImpuesto"
                        name="precioSinImpuesto"
                        onChange={(e) => this.syncField(e, 'precioSinImpuesto')}
                        fullWidth={true}
                        InputProps={{
                            inputComponent: NumberFormatCustom,
                        }}
                        margin="normal"
                    />
                    <TextField
                        error={this.state.error.precioConImpuesto ? true : false}
                        label={this.state.error.precioConImpuesto ? this.state.error.precioConImpuesto[0] : "Precio con Impuesto"}
                        value={this.state.precioConImpuesto}
                        placeholder="Precio com impuesto"
                        ref="precioConImpuesto"
                        name="precioConImpuesto"
                        onChange={(e) => this.syncField(e, 'precioConImpuesto')}
                        fullWidth={true}
                        InputProps={{
                            inputComponent: NumberFormatCustom,
                        }}
                        margin="normal"
                    />
                    <TextField
                        error={this.state.error.cantidad ? true : false}
                        label={this.state.error.cantidad ? this.state.error.cantidad[0] : "Cantidad"}
                        value={this.state.cantidad}
                        placeholder="Cantidad"
                        ref="cantidad"
                        name="cantidad"
                        onChange={(e) => this.syncField(e, 'cantidad')}
                        fullWidth={true}
                        margin="normal"
                    />
                </Grid>
            </Grid>
            {this.state.action ? <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit}> Registrar  </Button> :
                <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar}> Modificar  </Button>
            }



            <LisPropuestaEconomica  modificar={this.modificar}  onEliminar={this.onEliminar} datos={this.state.propuestaeconomica} action={true} />

        </div>
        );
    }
}
export default connect(mapSteToProps)(FormPropuestaEconomica);