import React from 'react';
import env from "../../../../config/env";
import axios from "axios";
import { connect } from 'react-redux';
import { store } from '../../../../../store/store';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';

function mapSteToProps(state) {
    return { userAcceso: state.userAcceso,  idEmpresa: state.propuesta.idEmpresa }
}

class Reference  extends React.Component{
    constructor(props) {
        super(props);

        this.state = {
            id: '',  
            comentarios: '',
           acepta: '', 
            estado:false, 
            msg: {
                estado: false,
                msg: '',
                tipo: ''
            },
            canSubmit: true,
            action: true,
        };
    }

    
  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }

  componentDidMount() {
    this.getData(this.props.idEmpresa);
 }
  
  componentWillReceiveProps(nextProps) {
    this.getData(nextProps.idEmpresa);
}

    async getData(id) {
        try {
          //detallereferencetermclientes/idEmpresa/idreferenceterm
          let res = await axios({
            url: env.endpoint + '/detallereferencetermclientes/' + id +'/'+ this.props.datos.id,
            method: 'get',
            timeout: 8000,
            headers: {
              'Accept': '*/*',
              'Authorization': this.props.userAcceso
            }
          })
          if(res.status === 200){
            // peticion correctamente
            this.setState({
              action: false,
              id: res.data.id, 
              comentario: res.data.comentario,
              estado: res.data.cumple
            })
        }else{
            this.setState({
                comentario: '',
                estado: false
              })
        }
        }
        catch (error) {
            //console.log( error);
         // this.getError(error.response);
        }
      }

    updateStado = () => {
        this.setState({
            estado: !this.state.estado,
        });
    }
    syncField(ev, fieldName) {
        let element = ev.target;
        let value = element.value;
        let jsonState = {};
        jsonState[fieldName] = value;
        this.setState(jsonState);
    }
render(){

return(<TableRow>
    <TableCell> {this.props.datos.Especificacion}</TableCell>
    <TableCell> {this.props.datos.observaciones}</TableCell>
    <TableCell> {this.props.datos.cantidad}</TableCell>
    <TableCell> {this.state.comentario} </TableCell>
    <TableCell> {this.state.estado? 'SI': 'NO'} </TableCell>
        </TableRow>);
}
} 
export default connect(mapSteToProps)(Reference);