import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Pagination from "react-js-pagination"; 
import Icon from '@material-ui/core/Icon';

const styles = {
    tableWrapper: {
        overflowX: 'auto',
    },
};


export class ListProvedorData extends React.Component {

  constructor(props) {
    super(props);
    this.state= {
    value: 0,
    
   }
  }

  modificar(id) {
    this.props.modificar(id);
}


    getMapData() {
        if (this.props.datos.data.length > 0) {
            return this.props.datos.data.map(row => {
                return (
                    <TableRow key={row.id}>
                        <TableCell > {row.nombre} </TableCell>
                        <TableCell >{row.representate}</TableCell>
                        <TableCell >  {row.nit}</TableCell>
                        <TableCell >{row.telefono}</TableCell>
                        <TableCell >  {row.email}</TableCell>
                        <TableCell >  {row.direccion}</TableCell>
                        <TableCell >  {row.ciudad}</TableCell>
                        <TableCell >  <Icon color="primary" onClick={() => this.modificar(row.id)} >edit_icon</Icon>  </TableCell>
                    </TableRow>
                );
            });
        }
        return "";
    }
    setData() {
        return (<Paper >
            <div style={styles.tableWrapper}>
                <Table  >
                    <TableHead>
                        <TableRow>
                        <TableCell>Nombre</TableCell>
                            <TableCell>representate</TableCell>
                            <TableCell >NIT</TableCell>
                            <TableCell >telefono</TableCell>
                            <TableCell >Email</TableCell>
                            <TableCell >Direccion</TableCell> 
                            <TableCell >Ciudad</TableCell> 
                            <TableCell >Acciones</TableCell> 
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {this.getMapData()}
                    </TableBody>
                </Table>


                {this.props.datos.data.length > 0? 
         <Pagination
          activePage={this.props.activePage}
          itemsCountPerPage={10}
          totalItemsCount={this.props.datos.total}
          pageRangeDisplayed={5}
          onChange={this.props.handlePageChange}
        />: '' }
      </div>
        </Paper>);
    }
    render() {
        return this.setData();
    }
}
export default ListProvedorData;