
function compras(state = {}, action) {
   switch(action.type) {
       case 'SET_USER_DATA':{
          return{...state, ...action.payload}
       }
       case 'SET_USER_ACCESO':{
         return{...state, ...action.payload}
      }
      
   case 'SET_SUGGESTION_LIST':{
    return{...state, ...action.payload}
   }
   case 'SET_propuesta':{
      return{...state, ...action.payload}
     }
  
     
// DATA COMPRA
   case 'SET_COMPRA_USER':{
      return{...state, ...action.payload}
     }
     case 'SET_COMPRA_DATA':{
      return{...state, ...action.payload}
     }
     case 'SET_COMPRA_PRESUPUESTO':{
      return{...state, ...action.payload}
     }
     case 'SET_COMPRA_TERMINOS':{
      return{...state, ...action.payload}
     }
     case 'SET_COMPRA_ENTRAGABLE':{
      return{...state, ...action.payload}
     }




   default:
    return state
   }
}
export default compras;