import React from 'react';
import { connect } from 'react-redux';
import Button from '@material-ui/core/Button';
import { store } from '../../../store/store'
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import axios from "axios";
import env from '../../config/env';
import logo from '../../../assets/LOGODXC-01.jpg';
const styles = {
  root: {
    width: '98%',
    
  },
   cont: {
    display: 'flex',
    justifyContent: 'center', 
   
    
  }, logo:{
    width: '30%', 
    display: 'block',
    marginLeft: 'auto',
    marginRight: 'auto',
  }, 
  main:{
    padding: '10%'
  }
};

function mapSteToProps(state) {
  return {  userAcceso: state.userAcceso }
}

class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      password: '',
      error:false, 
      canSubmit: true,
      action: true,
    };
  }

  componentDidMount() {
    if(this.props.userAcceso){
      this.props.history.push('/home') //lo redirigimos si esta logueado jaja
    }
}
  home(){ 
    this.props.history.push('/home')
  }

  syncField(ev, fieldName) { // data form
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
  }


  onSubmit = () => {
    this.setState({
      error: false, 
    })
    axios({
      method: 'post',
      url: env.endpoint + '/authenticate',
      data: {
        email: this.state.email,
        password: this.state.password,
      }, headers: {
        'Accept': '*/*',
      }

    }).then(response => {
      //registramos en el storage token de acceso.
      // es el unico nesesario por temas de seguridad para obtener la session de token deben decodificar el mismo
      store.dispatch({
        type: 'SET_USER_ACCESO',
        payload: {
          userAcceso: response.data.auth_token
        }
      })
      this.home();
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        if(error.response.status === 500 ){
        this.setState({
          error: true, 
        })
        }
      });
  }
  render() {
    return (
      <div style={styles.cont}>
        <div style={styles.main} >
          <Paper style={styles.main}>
          <img  style={styles.logo}  src={logo} alt="logo"  />
{this.state.error?    <p style={{color:'red'}}> Error Usuario o contrasea errneos.</p>:''}
       
            <TextField
              id="outlined-email-input"
              label="Email"
              type="email"
              name="email"
              autoComplete="email"
              onChange={(e) => this.syncField(e, 'email')}
              fullWidth={true}
              margin="normal"
              variant="outlined"
            />
            <TextField
              id="outlined-password-input"
              label="Password"
              type="password"
              autoComplete="current-password"
              onChange={(e) => this.syncField(e, 'password')}
              fullWidth={true}
              margin="normal"
              variant="outlined"
            />
            <Button variant="contained" type="submit" color="primary" onClick={this.onSubmit}> Login  </Button>
          </Paper>
        </div>


        
     
   
      </div>
    );
  }
}
export default connect(mapSteToProps)(Login);
