import React from 'react';

import TextField from '@material-ui/core/TextField';
import env from "../../../config/env";
import axios from "axios";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';

function mapSteToProps(state) {
    return { userAcceso: state.userAcceso }
}
/**
 * Validar que este cliente tenga propuestas con la compra
 */

class FormPropuesta extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            id: '',
            description: '',
            msg: {
                estado: false,
                msg: '',
                tipo: ''
            },
            canSubmit: true,
            action: true,
        };
    }

    syncField(ev, fieldName) {
        let element = ev.target;
        let value = element.value;
        let jsonState = {};
        jsonState[fieldName] = value;
        this.setState(jsonState);
    }

    componentDidMount() {
        this.getData();
        this.props.updateSiguiente(false);
    }
    async getData() {
        try {
            let res = await axios({
                ///propuesta/idEmpresa/idCompra
                url: env.endpoint + '/propuesta/' + this.props.provedor.id + '/' + this.props.compra.id,
                method: 'get',
                timeout: 8000,
                headers: {
                    'Accept': '*/*',
                    'Authorization': this.props.userAcceso
                }
            })
            if (res.status === 200) {
                // peticion correctamente
                this.setState({
                    action: false,
                    id: res.data.id,
                    description: res.data.description,
                })
                this.props.propuesta(res.data);
            } else if (res.status === 202) {
                console.log(res.data.error);
            }
            // Don't forget to return something   
            return res.data
        }
        catch (error) {
            let errorObject = JSON.parse(JSON.stringify(error));
            console.log(errorObject);
        }
    }



    onSubmit = (e) => {
        this.setState({ canSubmit: false });
        axios({
            method: 'POST',
            url: env.endpoint + '/proposals',
            data: {
                description: this.state.description,
                compra_id: this.props.compra.id,
                empresa_id: this.props.provedor.id,
            },
            headers: {
                'Accept': '*/*',
                'Authorization': this.props.userAcceso
            }
        }).then(response => {

            this.props.dataPropuesta(response.data);
            this.props.handleNext();
        })
            .catch(error => {
                this.setState({ canSubmit: true });
                console.log('error');
            });
    }
    onSubmitEditar = (e) => {
        this.setState({ canSubmit: false });
        axios({
            method: 'put',
            url: env.endpoint + '/proposals/' + this.state.id,
            data: {
                description: this.state.description,
            },
            headers: {
                'Accept': '*/*',
                'Authorization': this.props.userAcceso
            }
        }).then(response => {
            console.log(response.data);
            this.props.dataPropuesta(response.data);
            this.props.handleNext();
        })
            .catch(error => {
                this.setState({ canSubmit: true });
                console.log('error');
            });
    }
    clear() {
        this.setState({
            canSubmit: true,
            id: '',
            description: '',
        });
    }
    

    render() {
        return (<div >
            <Grid container spacing={24}>
                <p>A continuacion podra describir las observaciones del cliente para la propuesta que va ha presentar.</p>
                <Grid item xs={12} sm={12}>
                    <TextField
                        label="Observaciones"
                        value={this.state.description}
                        placeholder="Escribe las observaciones del provedor para esta compra"
                        ref="Observaciones"
                        name="Observaciones"
                        onChange={(e) => this.syncField(e, 'description')}
                        fullWidth={true}
                        margin="normal"
                        multiline
                        variant="outlined"// 
                    />
                </Grid>
            </Grid>
            <Button onClick={ this.props.handleBack} > anterior </Button>

            {this.state.action ? <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit}> Registrar  </Button> :
                <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar}> Modificar  </Button>
            }
        </div>
        );
    }
}
export default connect(mapSteToProps)(FormPropuesta);