import React from 'react';

import TextField from '@material-ui/core/TextField';
import env from "../config/env";
import axios from "axios";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';


import { connect } from 'react-redux';
import InputLabel from '@material-ui/core/InputLabel';
import { store } from '../../store/store';
import ListPeriodo from './list-periodo';


function mapSteToProps(state) {
    return { userAcceso: state.userAcceso }
  }


class FormPeriodo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id: '',
            nombre: '',
            descripcion: '',
            fechaInicio: '',
            fechaFin: '',
            activo: false,
            datos: [],
            error: {},
            canSubmit: true,
            action: true,
        };
    }

    componentDidMount() { 
     this.getData();

    }

    getError(response) {
        switch (response.status) {
            case 401:
                store.dispatch({
                    type: 'SET_USER_ACCESO',
                    payload: {
                        userAcceso: ''
                    }
                })
                break;
            case 422:
                this.setState({
                    error: response.data
                })
                break;
            case 404:
                console.log("error 404, archivo no encontrado")
                break;
            default:
                console.log('error inesperado')
                break;
        }
    }

    syncField(ev, fieldName) { // data form
        let element = ev.target;
        let value = element.value;
        let jsonState = {};
        jsonState[fieldName] = value;
        this.setState(jsonState);
    }

    async getData() {
        try {
          let res = await axios({
            url: env.endpoint + '/periodos',
            method: 'get',
            timeout: 8000,
            headers: {
              'Accept': '*/*',
              'Authorization': this.props.userAcceso
            }
          })
          if (res.status === 200) {
            // peticion correctamente
            this.setState({ datos: res.data });
    
          }
        }
        catch (error) {
          this.getError(error.response);
        }
      }

      modificar = (id) => {
        this.getperiodo(id)
      }
      async getperiodo(id) { // Obtener los datos para modificar 
        try {
          let res = await axios({
            url: env.endpoint + '/periodos/' + id,
            method: 'get',
            timeout: 8000,
            headers: {
              'Accept': '*/*',
              'Authorization': this.props.userAcceso
            }
          })
          if (res.status === 200) {
            this.setState({
              action: false,
              id: res.data.id,
              nombre: res.data.nombre,
              descripcion: res.data.descripcion,
              fechaInicio: res.data.fechaInicio,
              fechaFin: res.data.fechaFin,
              activo: res.data.activo,
            })
          }
        } catch (error) {
          this.getError(error.response);
        }
      }


    updateStado = () => {
        this.setState({
            activo: !this.state.activo,
        });
    }


    onSubmit = (e) => {
        this.setState({ canSubmit: false });
        axios({
          method: 'post',
          url: env.endpoint + '/periodos',
          data: {
            nombre: this.state.nombre,
            descripcion: this.state.descripcion, 
            fechaInicio: this.state.fechaInicio,
            fechaFin: this.state.fechaFin,
            activo: this.state.activo
          }, headers: {
            'Accept': '*/*',
            'Authorization': this.props.userAcceso
          }
        }).then(response => {
          if (response.status === 203) {
            // peticion correctamente
            this.setState({
              Mensaje: true,
              error: { activo: response.data.Error },
              DatosPresupuesto: response.data,
              canSubmit: true
            })
            
          } else {
            this.clear();
            this.getData();
            this.setState({ canSubmit: true });
          }
        })
          .catch(error => {
            this.setState({ canSubmit: true });
            this.getError(error.response);
          });
    
      }


      onSubmitEditar = (e) => {
        this.setState({ canSubmit: false });
        axios({
          method: 'put',
          url: env.endpoint + '/periodos/' + this.state.id,
          data: {
            nombre: this.state.nombre,
            descripcion: this.state.descripcion, 
            fechaInicio: this.state.fechaInicio,
            fechaFin: this.state.fechaFin,
            activo: this.state.activo
          }, headers: {
            'Accept': '*/*',
            'Authorization': this.props.userAcceso
          }
        }).then(response => {
          if (response.status === 203) {
            // peticion correctamente
            this.setState({
              Mensaje: true,
              error: { activo: response.data.Error },
              DatosPresupuesto: response.data,
              canSubmit: true
            })
            
          } else {
            this.clear();
            this.getData();
            this.setState({ canSubmit: true });
          }
        })
          .catch(error => {
            this.setState({ canSubmit: true });
            this.getError(error.response);
          });
    
      }

      clear () {
        this.setState({
            action: true, 
            id: '',
            nombre: '',
            descripcion: '',
            fechaInicio: '',
            fechaFin: '',
            activo: false,
            error: {},
        })
      }

      listData() {
        if (this.state.datos.length !== 0) {
          return  <ListPeriodo  datos={this.state.datos} modificar={this.modificar} onEliminar={this.onEliminar} action={true} />;
        } else {
          return "";
        }
      }

      onEliminar = (id) => {
        axios({
          method: 'DELETE',
          url: env.endpoint + '/periodos/' + id,
          headers: {
            'Accept': '*/*',
            'Authorization': this.props.userAcceso
          }
        }).then(response => {
          this.getData();
        })
          .catch(error => {
            this.setState({ canSubmit: true });
            this.getError(error.response);
          });
      }
    render() {
        return (<div>
            <Grid container spacing={24}>
                <Grid item xs={12} sm={6}>
                    <TextField
                        error={this.state.error.nombre ? true : false}
                        label={this.state.error.nombre ? this.state.error.nombre[0] : "Nombre"}
                        placeholder="Nombre presupuesto"
                        value={this.state.nombre}
                        onChange={(e) => this.syncField(e, 'nombre')}
                        fullWidth={true}
                    />
                    <TextField
                        error={this.state.error.descripcion ? true : false}
                        label={this.state.error.descripcion ? this.state.error.descripcion[0] : "Descripcion"}
                        placeholder="Descripcin de presupuesto"
                        value={this.state.descripcion}
                        onChange={(e) => this.syncField(e, 'descripcion')}
                        fullWidth={true}
                        margin="normal"
                    />
                </Grid>

                <Grid item xs={12} sm={3}>
                    <TextField
                        error={this.state.error.fechaInicio ? true : false}
                        label={this.state.error.fechaInicio ? this.state.error.fechaInicio[0] : "Fecha de inicio"}
                        id="date"
                        type="date"
                        ref="FechaInicio"
                        name="FechaInicio"
                        value={this.state.fechaInicio}
                        onChange={(e) => this.syncField(e, 'fechaInicio')}
                        fullWidth={true}
                        margin="normal"
                        InputLabelProps={{
                            shrink: true,
                        }}
                    />
                </Grid>
                <Grid item xs={12} sm={3}>
                    <TextField
                        error={this.state.error.fechaFin ? true : false}
                        label={this.state.error.fechaFin ? this.state.error.fechaFin[0] : "Fecha de finalizacin"}
                        id="date"
                        type="date"
                        value={this.state.fechaFin}
                        ref="fechaFin"
                        name="fechaFin"
                        onChange={(e) => this.syncField(e, 'fechaFin')}
                        fullWidth={true}
                        margin="normal"
                        InputLabelProps={{
                            shrink: true,
                        }}
                    />

                    <InputLabel htmlFor="estado">{this.state.error.activo ? this.state.error.activo[0] : "Estado "}</InputLabel>
                    <FormControlLabel
                        control={
                            <Switch
                                checked={this.state.activo}
                                onChange={(e) => this.updateStado(e, 'rta')}
                                value='activo'
                            />
                        }
                    />
                </Grid>
            </Grid>
            {this.state.action ?
                <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit}> Registrar  </Button> :
                <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar}> Modificar  </Button>
            }

{ this.listData() }

        </div>
        );
    }
}


export default connect(mapSteToProps)(FormPeriodo);