import React from 'react';


import TextField from '@material-ui/core/TextField';
import env from "../../../config/env";
import axios from "axios";
import FormControlLabel from '@material-ui/core/FormControlLabel';
import InputLabel from '@material-ui/core/InputLabel';
import Switch from '@material-ui/core/Switch';
import { connect } from 'react-redux';
import { store } from '../../../../store/store';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import Icon from '@material-ui/core/Icon';
import CircularProgress from '@material-ui/core/CircularProgress';

function mapSteToProps(state) {
    return { userAcceso: state.userAcceso }
}

class TerminosReferenciaAceptar  extends React.Component{
    constructor(props) {
        super(props);

        this.state = {
            id: '',  
            comentarios: '',
           acepta: '', 
            estado:false, 
            msg: {
                estado: false,
                msg: '',
                tipo: ''
            },
            canSubmit: true,
            action: true,
        };
    }

    
  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }

  
    componentDidMount() {
       this.getData();
    }

    async getData() {
        try {
          //detallereferencetermclientes/idEmpresa/idreferenceterm
          let res = await axios({
            url: env.endpoint + '/detallereferencetermclientes/' + this.props.provedor.id +'/'+ this.props.datos.id,
            method: 'get',
            timeout: 8000,
            headers: {
              'Accept': '*/*',
              'Authorization': this.props.userAcceso
            }
          })
          if(res.status === 200){
            // peticion correctamente
            this.setState({
              action: false,
              id: res.data.id, 
              comentario: res.data.comentario,
              estado: res.data.cumple
            })
        }
        }
        catch (error) {
            console.log( error);
         // this.getError(error.response);
        }
      }


onSubmit = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'post',
      url: env.endpoint + '/detallereferencetermclientes',
      data: {
        comentario: this.state.comentario,
        cumple: this.state.estado? 1 : 0,
        empresa_id:this.props.provedor.id, 
        referenceterm_id:this.props.datos.id ,

      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }

    }).then(response => {
      this.setState({
        action: false,
        canSubmit: true , 
        id: response.data.id, 
        comentario: response.data.comentario,
        estado: response.data.cumple
      })

    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }


  onSubmitEditar = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'put',
      url: env.endpoint + '/detallereferencetermclientes/' + this.state.id,
      data: {
        comentario: this.state.comentario,
        cumple: this.state.estado? 1 : 0,
      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }

    }).then(response => {

      this.setState({
        action: false,
        canSubmit: true , 
        id: response.data.id, 
        comentario: response.data.comentario,
        estado: response.data.cumple
      })
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }
    updateStado = () => {
        this.setState({
            estado: !this.state.estado,
        });
    }
    syncField(ev, fieldName) {
        let element = ev.target;
        let value = element.value;
        let jsonState = {};
        jsonState[fieldName] = value;
        this.setState(jsonState);
    }
render(){

return(<TableRow>
    <TableCell> {this.props.datos.Especificacion}</TableCell>
    <TableCell> {this.props.datos.observaciones}</TableCell>
    <TableCell> {this.props.datos.cantidad}</TableCell>
           
    <TableCell>  <TextField
                label="Observaciones"
                 value={this.state.comentario}
                placeholder="Observaciones"
                ref="Observaciones"
                name="Observaciones"
                onChange={(e) => this.syncField(e, 'comentario')}
                fullWidth={true}
                margin="normal"
                multiline
            />
            </TableCell>
            <TableCell>
            <InputLabel htmlFor="estado">{this.state.estado?'SI':'NO'} </InputLabel>
                    <FormControlLabel
                        control={
                            <Switch
                               checked={this.state.estado}
                                onChange={(e) => this.updateStado(e, 'estado')}
                                value='activo'
                            />
                        }
                    />
              </TableCell>
            
        
        {
          !this.state.canSubmit? <CircularProgress  color="secondary" />: (
          
          this.state.action ?( <Icon variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit} >done_all</Icon> ):
      (<Icon variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar} >done_all</Icon>)
           ) }
        </TableRow>);
}
} 
export default connect(mapSteToProps)(TerminosReferenciaAceptar);