
import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
// import MenuIcon from '@material-ui/icons/Menu';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import { store } from '../../store/store';
import { connect } from 'react-redux';
import Drawer from '@material-ui/core/Drawer';
import MenuApp from './menuapp';
import LinkButton from './LinkButton';
import jwtDecode from "jwt-decode";

function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}

const styles = {
  root: {
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
  list: {
    width: 250,
  },
  appBar: {
    top: 'auto',
  },
  fullList: {
    width: 'auto',
  },
  logo:{
    width: '9%',
    padding: '5px'
  }
};


class MenuAppBar extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      anchorEl: null,
      user:{} , 
      top: false,
      left: false,
      bottom: false,
      right: false,
    }
  }
  toggleDrawer = (side, open) => () => {
    this.setState({
      [side]: open,
    });
  };
  componentDidMount() {
    this.validarstado();
  }
  validarstado = () => {
    if (this.props.userAcceso !== null && this.props.userAcceso !== '') {
      this.setState({ auth: true });
    }
  };

  logout = (e) => {
    this.handleClose();
    //registramos en el storage token de acceso.
    store.dispatch({
      type: 'SET_USER_ACCESO',
      payload: {
        userAcceso: ''
      }
    })

  }


  handleChange = event => {
    this.setState({ auth: event.target.checked });
  };

  handleMenu = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null });
  };
  render() {
    const { classes } = this.props;
    const { anchorEl } = this.state;
    const open = Boolean(anchorEl);


    return (
      <div className={classes.root}>
        <AppBar position="static">
          <Toolbar>
            {/*
  
             this.props.userAcceso && (<Button className={classes.menuButton} color="inherit" aria-label="Menu" onClick={this.toggleDrawer('left', true)}>
              <MenuIcon />
            </Button>) */}
            
            <Typography variant="h6" color="inherit" className={classes.grow}>
              Dividendo Compras
            </Typography>
            {!this.props.userAcceso && (
               <LinkButton to='/login' >Login</LinkButton>
            )}
            {this.props.userAcceso && (
              <div>
                {
                  //user Super-Admin
                  jwtDecode(this.props.userAcceso).rol=== 'Super-Admin'? 
                  <LinkButton to='/Presupuesto' >Presupuesto</LinkButton>: ''
                }
                 {
                  //user Super-Admin
                  jwtDecode(this.props.userAcceso).rol=== 'Super-Admin'? 
                  <LinkButton to='/AdminUser' >funcionarios</LinkButton> : ''
                }
                 {
                  //user Super-Admin
                  jwtDecode(this.props.userAcceso).rol=== 'Super-Admin'? 
                  <LinkButton to='/AdminProvedores' >Provedores</LinkButton> : ''
                }
                 {
                  //user Super-Admin
                  jwtDecode(this.props.userAcceso).rol=== 'Super-Admin'? 
                  <LinkButton to='/AdminMaestros' >Datos Maestros</LinkButton> : ''
                }

                <LinkButton to='/compra' >Compra</LinkButton>
                <LinkButton to='/compras' >Compras</LinkButton>
              </div>

            )}
            {this.props.userAcceso && (
              <div>
                <IconButton
                  aria-owns={open ? 'menu-appbar' : undefined}
                  aria-haspopup="true"
                  onClick={this.handleMenu}
                  color="inherit"
                >
                  <AccountCircle />
                </IconButton>
                <Menu
                  id="menu-appbar"
                  anchorEl={anchorEl}
                  anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  open={open}
                  onClose={this.handleClose}
                >
                  <MenuItem onClick={this.handleClose}>My account {jwtDecode(this.props.userAcceso).name}</MenuItem>
                  <MenuItem onClick={this.logout}>logout</MenuItem>
                </Menu>
              </div>
            )}
          </Toolbar>
        </AppBar>
        <Drawer open={this.state.left} onClose={this.toggleDrawer('left', false)}>
          <div
            tabIndex={0}
            role="button"
            onClick={this.toggleDrawer('left', false)}
            onKeyDown={this.toggleDrawer('left', false)}
          >
            <MenuApp className={classes.list} />
          </div>
        </Drawer>
      </div>
    );
  }
}

MenuAppBar.propTypes = {
  classes: PropTypes.object.isRequired,
};
export default connect(mapSteToProps)(withStyles(styles)(MenuAppBar));