import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Icon from '@material-ui/core/Icon';
import DeleteIcon from '@material-ui/icons/Delete';

const styles = {
    tableWrapper: {
        overflowX: 'auto',
    },
};

export class LisDetallePresupuesto extends React.Component {

    modificar(id) {
        this.props.modificar(id);
    }

    eliminar(id) {
        this.props.onEliminar(id);
    }
    getMapData() {

        if (this.props.datos) {
            return this.props.datos.data.map(row => {
                return (
                    <TableRow key={row.id}>
                        <TableCell > {row.servicio} </TableCell>
                        <TableCell >{row.componenteName}</TableCell>
                        <TableCell >{row.clusterName}</TableCell>
                        <TableCell >{row.operationName}</TableCell>
                        <TableCell >{row.cantidad}</TableCell>
                    
                        <TableCell >{
                            new Intl.NumberFormat('COL', {
                                style: 'currency',
                                currency: 'COL',
                                minimumFractionDigits: 0,
                                maximumFractionDigits: 0
                            }).format(row.valorConImpuesto)
                        }</TableCell>
                        {this.props.action ? <TableCell >
                            <Icon color="primary" onClick={() => this.modificar(row.id)} >edit_icon</Icon>
                            <DeleteIcon color="error" onClick={() => this.eliminar(row.id)} />
                        </TableCell> : <TableCell ></TableCell>}
                    </TableRow>
                );
            });
        }
        return "";
    }
    setData() {
        return (<Paper >
            <div style={styles.tableWrapper}>

                <Table  >
                    <TableHead>
                        <TableRow>
                            <TableCell>Servicio</TableCell>
                            <TableCell >componente</TableCell>
                            <TableCell >cluster</TableCell>
                            <TableCell >Centro de operacin</TableCell>
                            <TableCell >Cantidad</TableCell>
                            <TableCell >$ valor con impuesto</TableCell>
                            {this.props.action ? <TableCell >Acciones</TableCell> : <TableCell ></TableCell>}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {this.getMapData()}
                        <TableRow >
                            <TableCell rowSpan={3} />
                            <TableCell colSpan={5}>Total de compra</TableCell>
                            <TableCell align="right">{
                                new Intl.NumberFormat('COL', {
                                    style: 'currency',
                                    currency: 'COL',
                                    minimumFractionDigits: 0,
                                    maximumFractionDigits: 0
                                }).format(this.props.datos.total)}</TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
            </div>
        </Paper>);
    }


    render() {
        return this.setData();
    }
}

export default LisDetallePresupuesto;