import React from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import jwtDecode from "jwt-decode";
import ListComprasPendientes from './list-ComprasPendientes';
import env from "../../../components/config/env";
import axios from "axios";
import { store } from '../../../store/store';
import FormBuscarUser from '../../user/form-buscar-user';

function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}

function TabContainer({ children }) {
  return (
    <div style={{ padding: 8 * 3 }}>
      {children}
    </div>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired
};

const styles = {

  cont: {
    display: 'flex',
    justifyContent: 'center'
  },
};

class InfoUser extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 0,
      user: { id: 0 },
      dataCompra: []
    };
  }


  componentDidMount() {
    this.props.btns(false);
    this.getData();
  }

  handleChange = (event, value) => {
    this.setState({ value });
  };

  handleChangeIndex = index => {
    this.setState({ value: index });
  };



  getData() {
    if (this.props.userData.id === 0) {
      this.getUser(jwtDecode(this.props.userAcceso).user_id);
    } else {
      this.getUser(this.props.userData.id);
    }
  }


  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      case 404:
        console.log("error 404, archivo no encontrado")
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }

  async getUser(id) {
    try {
      let res = await axios({
        ///users/iduser
        url: env.endpoint + '/users/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {

        this.setState({
          user: res.data
        })
        this.getComprasPersonTipe(id);
      }
    }
    catch (error) {
      this.getError(error.response);
    }
  }



  async getComprasPersonTipe(id) { // Obtener los datos
    try {
      let res = await axios({
        url: env.endpoint + '/compras/' + id + '/0',
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      this.setState({
        dataCompra: res.data
      })

    } catch (error) {
      this.getError(error.response);
    }
  }

  getcompraID = (id) => {
    this.props.compraData({ id: id });
    this.props.USER(this.state.user);
  }
  confirmar = () => {
    this.props.USER(this.state.user);
  }

  pasarUser = (data) => {
    this.getUser(data.user_id);
  }


  render() {
    return (
      <div >
        <p>Titular de la compra</p>

        {jwtDecode(this.props.userAcceso).rol === 'Super-Admin' || jwtDecode(this.props.userAcceso).rol === 'Colaborador'?
        (<Grid container spacing={24}>
        <Grid item xs={12} sm={6}>
          <FormBuscarUser  pasarUser={this.pasarUser} />
        </Grid> </Grid>
        )
        : ''
      }
        <Grid container spacing={24}>
          <Grid item xs={12} sm={6}>

            <p><b>Solicitante :</b> {this.state.user.name}   {this.state.user.apellido}
            </p>
            <p><b>Cargo:</b> {this.state.user.cargo}
            </p>
            <p><b>Direccin :</b> {this.state.user.direccion}
            </p>
          </Grid>
          <Grid item xs={12} sm={6}>
            <p><b>Identificacin:</b> {this.state.user.documento}
            </p>
            <p><b>E-mail:</b> {this.state.user.email}
            </p>
          </Grid>
        </Grid>
        <div style={styles.cont} >
          <Button variant="contained" color="primary" onClick={this.confirmar}> Continuar </Button>
        </div>

        {this.state.dataCompra.length > 0 ? (<div>
          <p>Termina de gestionar tus compras</p>
          <ListComprasPendientes getcompraID={this.getcompraID} datos={this.state.dataCompra} action={true} />
        </div>) : ' '
        }
      </div>
    );
  }
}

InfoUser.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default connect(mapSteToProps)(InfoUser);


