import React from 'react';
import TextField from '@material-ui/core/TextField';
import env from "../../config/env";
import axios from "axios";
import Button from '@material-ui/core/Button';
import Entregables from './entregables';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import { store } from '../../../store/store';


function mapSteToProps(state) {
  return { User: state.UserData, userAcceso: state.userAcceso }
}


class FormEntregables extends React.Component {


  constructor(props) {
    super(props);

    this.state = {
      id: '',
      producto: '',
      observaciones: '',
      cantidad: 0,
      referenceterm: [],
      action: true,
      error: {},
      msg: {
        estado: false,
        msg: '',
        tipo: ''
      },
      canSubmit: true,
    };
  }


  //validamos errores 
  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      case 404:
        console.log("error 404, archivo no encontrado")
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }
  //hacer que se ejecute una funcion de primeras
  componentDidMount() {
     this.getData(this.props.compra.id);
    this.props.btns(true);
  }

  async getData(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/listEntregables/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ referenceterm: res.data });
        if (this.state.referenceterm.length === 0) {
          this.props.CanNext(false);
        } else { this.props.CanNext(true); }
        this.listData();

      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  modificar = (id) => {
    this.getConsultarPorid(id);
  }

  async getConsultarPorid(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/entregables/' + id,
        method: 'get',
        timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({
          canSubmit: true,
          action:false, 
          id: res.data.id, 
          producto: res.data.producto,
          observaciones: res.data.observaciones,
          cantidad: res.data.cantidad
        });

      }
      // Don't forget to return something   
      return res.data
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  listData() {
    if (this.state.referenceterm.length !== 0) {
      return <Entregables datos={this.state.referenceterm} modificar={this.modificar} onEliminar={this.onEliminar} action={true} />;
    } else {
      return "";
    }
  }

  onEliminar = (id) => {
    axios({
      method: 'DELETE',
      url: env.endpoint + '/entregables/' + id,
      headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    }).then(response => {
      this.setState({
        canSubmit: true,
        action:true,
        producto: '',
        observaciones: '',
        cantidad: 0, 
        error: {},
      });
      this.componentDidMount()
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }
  onSubmit = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'post',
      url: env.endpoint + '/entregables',
      data: {
        producto: this.state.producto,
        observaciones: this.state.observaciones,
        cantidad: this.state.cantidad,
        compra_id: this.props.compra.id,
      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }

    }).then(response => {

      this.setState({
        canSubmit: true,
        action:true,
        producto: '',
        observaciones: '',
        cantidad: 0, 
        error: {},
      });

      this.componentDidMount();
      // this.props.msgbn('Datos registrados correctamente.');
    })
      .catch(error => {
        this.getError(error.response);
        this.setState({
          canSubmit: true,
        });
      });
  }





  onSubmitEditar= (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'PUT',
      url: env.endpoint + '/entregables/' + this.state.id,
      data: {
        producto: this.state.producto,
        observaciones: this.state.observaciones,
        cantidad: this.state.cantidad,
        compra_id: this.props.compra.id,
  
      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    }).then(response => {

      this.setState({
        action:true,
        canSubmit: true,
        producto: '',
        observaciones: '',
        cantidad: 0,
        error: {},
      });

      this.componentDidMount();
      // this.props.msgbn('Datos registrados correctamente.');
    })
      .catch(error => {
        this.getError(error.response);
        this.setState({
          canSubmit: true,
        });
      });
  }

  syncField(ev, fieldName) { // data form
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
  }
  render() {
    return (<div >
      <Grid container spacing={24}>
        <Grid item xs={6} sm={3}>
          <TextField
            error={this.state.error.producto ? true : false}
            label={this.state.error.producto ? this.state.error.producto[0] : "producto o servicio "}
            placeholder="producto o servicio "
            ref="producto"
            name="producto"
            onChange={(e) => this.syncField(e, 'producto')}
            value={this.state.producto}
            fullWidth={true}
            multiline
            margin="normal"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            error={this.state.error.observaciones ? true : false}
            label={this.state.error.observaciones ? this.state.error.observaciones[0] : "Observaciones "}
            placeholder="Expecificar Observaciones"
            ref="observaciones"
            name="observaciones"
            onChange={(e) => this.syncField(e, 'observaciones')}
            value={this.state.observaciones}
            fullWidth={true}
            multiline
            margin="normal"
          />
        </Grid>
        <Grid item xs={3} sm={1}>
          <TextField
            error={this.state.error.cantidad ? true : false}
            label={this.state.error.cantidad ? this.state.error.cantidad[0] : "Cantidad "}
            placeholder="Cantidad"
            ref="Cantidad"
            name="Cantidad"
            onChange={(e) => this.syncField(e, 'cantidad')}
            value={this.state.cantidad}
            fullWidth={false}
            multiline
            margin="normal"
          />
        </Grid>
      </Grid>
      {this.state.action ? <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit}> Registrar  </Button> :
        <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar}> Modificar  </Button>
      }
      {this.listData()}
    </div>
    )
  }
}

export default connect(mapSteToProps)(FormEntregables);