import React from 'react';
import env from "../../config/env";
import axios from "axios";

import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';


function mapSteToProps(state) {
    return { userAcceso: state.userAcceso , storePro: state.propuesta.id  }
}

class ListView extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            id: '', 
            comentarios: '',
            estado: false,
            msg: {
                estado: false,
                msg: '',
                tipo: ''
            },
            canSubmit: true,
            action: true,
        };
    }

    componentDidMount() {
       this.getData(this.props.storePro);
    }
    componentWillReceiveProps(nextProps) {
        this.getData(nextProps.storePro);
    }

    async getData(id) {
        try {
          let res = await axios({
            url: env.endpoint + '/politica/' + this.props.politica.id +'/'+ id,
            method: 'get',
            timeout: 8000,
            headers: {
              'Accept': '*/*',
              'Authorization': this.props.userAcceso
            }
          })
          if(res.status === 200){
            // peticion correctamente
            this.setState({
              id: res.data.id, 
              comentarios: res.data.comentarios,
              estado: res.data.acepta
            })
        }else{
            this.setState({
                comentarios: '',
                estado: false
              })
        }
        }
        catch (error) {
            //console.log( error);
         // this.getError(error.response);
        }
      }

    render() {
        return (
        <Grid container spacing={24}>
            <Grid item xs={12} sm={12}>
                {this.props.politica.pregunta}
            </Grid>
            <Grid item xs={12} sm={6}>
        <b>Observaciones:</b> {this.state.comentarios} 

            </Grid>
            <Grid item xs={12} sm={6}>
        <b>{this.state.estado? 'SI': 'NO'}</b> 
            </Grid>
        </Grid>
        )
    }
}
export default  connect(mapSteToProps)(ListView);