import React from 'react';

import TextField from '@material-ui/core/TextField';
import env from "../../config/env";
import axios from "axios";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import {store} from '../../../store/store';
import SimpleDialog from '../../Provedor/SimpleDialogWrapped';


function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}
class FormBuscarProvedor extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      nit: '',
      nombre: '', 
      empresas:[], 
      msg: {
        estado: false,
        msg: '',
        tipo: ''
      },
      open: false,
      error:{},
      canSubmit: true,
      action: true,
    };
  }

  handleClickOpen = () => {
    this.setState({
      open: true,
    });
  };


  handleSelect = value => {
    this.setState({ open: false });
    this.props.provedor(value);
    this.props.handleNext();
  };
  handleClose = value => {
    this.setState({ open: false });
  };
  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      case 404:
        console.log("error 404, archivo no encontrado")
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }


  syncField(ev, fieldName) {
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
  }

  onSubmit = (e) => {
    if(this.state.nit !== ''){
this.BuscarNit();
    }else{
      this.FilterNombre();
    }
  }

BuscarNit = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'get',
      timeout: 8000,
      url: env.endpoint + '/empresa/search/'+ this.state.nit,
      headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    }).then(response => {

      if(response.status === 203){
      this.setState({
       error: {nit: response.data.error}
      })
        this.clear();

      }else{
        this.props.provedor(response.data);
        this.props.handleNext();
      }
      
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }

  FilterNombre = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'get',
      timeout: 8000,
      url: env.endpoint + '/empresa/search/?nombre=' + this.state.nombre,
      headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    }).then(response => {
console.log(response.data.length);
      if(response.data.length === 0){
        this.setState({
          error: {nombre: ['No encontramos resultados']}
         })
           this.clear();
  }else{
    this.setState({
      canSubmit: true, 
      empresas: response.data
    }) 
    this.handleClickOpen();
    }
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }
clear(){
  this.setState({ canSubmit: true , 
    nombre: '',
    nit: ''
  });
}
  render() {
    return (<div >
      <Grid container spacing={24}>
        <Grid item xs={12} sm={6}>
          <TextField
            error={this.state.error.nombre ? true : false}
            label={this.state.error.nombre ? this.state.error.nombre[0] : "Nombre Empresa "}
             value={this.state.nombre}
            placeholder="Busca por nombre de empresa"
            ref="nombre"
            name="nombre"
            onChange={(e) => this.syncField(e, 'nombre')}
            fullWidth={true}
            margin="normal"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
           error={this.state.error.nit ? true : false}
           label={this.state.error.nit ? this.state.error.nit[0] : "NIT"}
            value={this.state.nit}
            placeholder="Busca por Nit"
            ref="nit"
            name="nit"
            onChange={(e) => this.syncField(e, 'nit')}
            fullWidth={true}
            type="number"
            margin="normal"
          />
        </Grid>
      </Grid>
      <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit}>Buscar </Button> 
      <SimpleDialog
          selectedValue={this.state.selectedValue}
          open={this.state.open}
          onClose={this.handleClose}
          handleSelect={this.handleSelect}
          datos={this.state.empresas}
        />
    </div>
    );
  }
}
export default connect(mapSteToProps)(FormBuscarProvedor);