import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import env from "../../../config/env";
import axios from "axios";
import { connect } from 'react-redux';
import { store } from '../../../../store/store';
import jwtDecode from "jwt-decode";

function mapSteToProps(state) {
    return { userAcceso: state.userAcceso }
}

class FormAceptar extends React.Component {
        constructor(props) {
          super(props);
          this.state = {
          open: false,
          Error: false, 
          MessageError: '', 
          canSubmit: true, 
        aprobada: false, 
        error:{},
        user:{}
        }
      }

      componentDidMount() {
        this.setState({
          user: jwtDecode(this.props.userAcceso),
        })
      }
  handleClickOpen = () => {
    this.setState({ open: true });
  };
  handleClose = () => {
    this.setState({ open: false });
  };
  syncField(ev, fieldName) {
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
}
getError(response) {
  switch (response.status) {
    case 401:
      store.dispatch({
        type: 'SET_USER_ACCESO',
        payload: {
          userAcceso: ''
        }
      })
      break;
    case 422:
      this.setState({
        error: response.data
      })
      break;
    case 404:
      console.log("error 404, archivo no encontrado")
      break;
    default:
      console.log('error inesperado')
      break;
  }
}

onSubmit = (e) => {
    this.setState({ canSubmit: false });
    axios({
        method: 'POST',
        url: env.endpoint + '/detallepropuesta',
        data: {
            descripcion: this.state.description,
            idPropuesta: this.props.propuesta.id,
            idCompra: this.props.compra.id, 
            user_id: this.state.user.user_id
        },
        headers: {
            'Accept': '*/*',
            'Authorization': this.props.userAcceso
        }
    }).then(response => {
        if (response.status === 203) {
            // peticion correctamente
            this.setState({
              Error: true,
              MessageError: response.data.Error,
              canSubmit: true ,
              error:{}
            })
          } else {
        this.props.inicio();
          }
     
    })
        .catch(error => {
            this.setState({ canSubmit: true });
            this.getError(error.response);
        });
}
  render() {
    return (
      <div>

        {jwtDecode(this.props.userAcceso).rol=== 'Super-Admin'?
      <Button variant="outlined" color="primary" onClick={this.handleClickOpen}>
      Aprobar propuesta
    </Button>: ''
      }
        <Dialog
          open={this.state.open}
          onClose={this.handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">{"Aprobar Propuesta"}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
             A continuacin puede aprobar esta propuesta pero tienes que llenar las observaciones.
            </DialogContentText>
            {this.state.Error?( <DialogContentText  style={{color: 'red'}} >
            {this.state.MessageError}
            </DialogContentText>): ''
            }
           
            <Grid container spacing={24}>
                <Grid item xs={12} sm={12}>
                    <TextField
                      error={this.state.error.descripcion ? true : false}
                      label={this.state.error.descripcion ? this.state.error.descripcion[0] : "Observaciones"} 
                        value={this.state.description}
                        placeholder="Escribe las observacin de aprobacin de esta propuesta."
                        ref="Observaciones"
                        name="Observaciones"
                        onChange={(e) => this.syncField(e, 'description')}
                        fullWidth={true}
                        margin="normal"
                        multiline
                        variant="outlined"// 
                    />
                </Grid>
            </Grid>
          </DialogContent>
          <DialogActions>
            <Button disabled={!this.state.canSubmit} onClick={this.onSubmit} color="primary">
              Aprobar
            </Button>
            <Button disabled={!this.state.canSubmit} onClick={this.handleClose} color="primary" autoFocus>
              Cancelar
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

export default connect(mapSteToProps)(FormAceptar);