import React from 'react';
import TextField from '@material-ui/core/TextField';
import env from "../../../config/env";
import axios from "axios";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import LisDetallePresupuesto from './lisdetallepresupuesto';
import { connect } from 'react-redux';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import { store } from '../../../../store/store';
import NumberFormatCustom from '../../../shared/number-format-custom';
import Card from '@material-ui/core/Card';

function mapSteToProps(state) {
  return { userAcceso: state.userAcceso }
}

class DetallePresupuesto extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      id: '',
      servicio: '',
      cantidad: 0,
      valorSinImpuesto: 0,
      valorConInpuesto: '',
      cluster_id: '',
      componente_id: '',
      operacion_id: '',
      proyecto_id: '', 
      datos: { data: [] },
      componentes: [],
      operacions: [],
      Cluster: [],
      Proyectos: [],
      Mensaje: false,
      DatosPresupuesto: {},
      msgProye:'Buscando', 
      msg: {
        estado: false,
        msg: '',
        tipo: ''
      },
      error: {},
      canSubmit: true,
      action: true,
    };
  }

  getError(response) {
    switch (response.status) {
      case 401:
        store.dispatch({
          type: 'SET_USER_ACCESO',
          payload: {
            userAcceso: ''
          }
        })
        break;
      case 422:
        this.setState({
          error: response.data
        })
        break;
      case 404:
        console.log("error 404, archivo no encontrado")
        break;
      default:
        console.log('error inesperado')
        break;
    }
  }

  syncField(ev, fieldName) { // data form
    let element = ev.target;
    let value = element.value;
    let jsonState = {};
    jsonState[fieldName] = value;
    this.setState(jsonState);
  }

  componentDidMount() {
    this.getValidate();
    this.getcluster();
    this.getComponente();
    this.getOperacion();
    //this.getProyectos();
  }

  getValidate() {
    if (this.props.compra.id != null) {
      this.getData(this.props.compra.id);
    }
  }

  modificar = (id) => {
    this.getDetallepresupuesto(id)
  }
  async getDetallepresupuesto(id) { // Obtener los datos para modificar el detalle del presupuestado
    try {
      let res = await axios({
        url: env.endpoint + '/detallepresupuestos/' + id,
        method: 'get',
        //  timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        this.setState({
          action: false,
          id: res.data.id,
          servicio: res.data.servicio,
          cantidad: res.data.cantidad,
          valorSinImpuesto: res.data.valorSinImpuesto,
          valorConInpuesto: res.data.valorConInpuesto,
          cluster_id: res.data.cluster_id,
          componente_id: res.data.componente_id,
          operacion_id: res.data.operacion_id,
        })
      }
    } catch (error) {
      this.getError(error.response);
    }
  }



  clear() {
    this.setState({
      id: '',
      servicio: '',
      cantidad: 0,
      valorSinImpuesto: '',
      valorConInpuesto: '',
      cluster_id: '',
      componente_id: '',
      operacion_id: '',
      action: true,
      error: {},
      Mensaje: false,
    })
  }

  async getData(id) {
    try {
      let res = await axios({
        url: env.endpoint + '/ListdetallePresupuesto/' + id,
        method: 'get',
        //  timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ datos: res.data });
        if (this.state.datos.data.length === 0) {
          this.props.CanNext(false);
          this.props.btns(false);
        } else {
          this.props.CanNext(true);
          this.props.btns(true);
        }
        this.listData();
      }
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  listData() {
    if (this.state.datos.data.length !== 0) {
      return <LisDetallePresupuesto datos={this.state.datos} modificar={this.modificar} onEliminar={this.onEliminar} action={true} />;
    } else {
      return "";
    }
  }

  onSubmit = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'post',
      url: env.endpoint + '/detallepresupuestos',
      data: {
        servicio: this.state.servicio,
        cantidad: this.state.cantidad,
        valorSinImpuesto: this.state.valorSinImpuesto,
        valorConInpuesto: this.state.valorConInpuesto,
        NombreCompra: this.state.NombreCompra,
        cluster_id: this.state.cluster_id,
        componente_id: this.state.componente_id,
        compra_id: this.props.compra.id,
        operacion_id: this.state.operacion_id,
        proyecto_id: this.state.proyecto_id, 
        periodo_id: this.props.periodo.id
      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    }).then(response => {
      if (response.status === 203) {
        // peticion correctamente
        this.setState({
          Mensaje: true,
          error: { valorConInpuesto: response.data.Error },
          DatosPresupuesto: response.data,
          canSubmit: true
        })
      } else {
        this.getValidate();
        this.clear();
        this.setState({ canSubmit: true });
      }
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });

  }
  onSubmitEditar = (e) => {
    this.setState({ canSubmit: false });
    axios({
      method: 'put',
      url: env.endpoint + '/detallepresupuestos/' + this.state.id,
      data: {
        servicio: this.state.servicio,
        cantidad: this.state.cantidad,
        valorSinImpuesto: this.state.valorSinImpuesto,
        valorConInpuesto: this.state.valorConInpuesto,
        NombreCompra: this.state.NombreCompra,
        cluster_id: this.state.cluster_id,
        componente_id: this.state.componente_id,
        operacion_id: this.state.operacion_id,
        periodo_id: this.props.periodo.id,
        proyecto_id: this.state.proyecto_id
      }, headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    }).then(response => {
      if (response.status === 203) {
        // peticion correctamente
        this.setState({
          Mensaje: true,
          error: { valorConInpuesto: response.data.Error },
          DatosPresupuesto: response.data,
          canSubmit: true
        })
      } else {
        this.getValidate();
        this.clear();
        this.setState({ canSubmit: true });
      }
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }

  onEliminar = (id) => {
    axios({
      method: 'DELETE',
      url: env.endpoint + '/detallepresupuestos/' + id,
      headers: {
        'Accept': '*/*',
        'Authorization': this.props.userAcceso
      }
    }).then(response => {
      this.getValidate();
    })
      .catch(error => {
        this.setState({ canSubmit: true });
        this.getError(error.response);
      });
  }


  async getcluster() {
    try {
      let res = await axios({
        url: env.endpoint + '/clusters/',
        method: 'get',
        //  timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ Cluster: res.data });
      }
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  async getComponente() {
    try {
      let res = await axios({
        url: env.endpoint + '/componentes/',
        method: 'get',
        //  timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ componentes: res.data });
      }
    }
    catch (error) {
      this.getError(error.response);
    }
  }


  async getOperacion() {
    try {
      let res = await axios({
        url: env.endpoint + '/operacions/',
        method: 'get',
        //  timeout: 8000,
        headers: {
          'Accept': '*/*',
          'Authorization': this.props.userAcceso
        }
      })
      if (res.status === 200) {
        // peticion correctamente
        this.setState({ operacions: res.data });
      }
    }
    catch (error) {
      this.getError(error.response);
    }
  }

  async getProyectos() {

    if(this.state.cluster_id > 0 && this.state.componente_id > 0 && this.state.operacion_id > 0  ){
      this.setState({
        msgProye:'Buscando',Proyectos:[] });
    
      try {
        let res = await axios({
          url: env.endpoint + '/proyecto/f/?periodo_id=1&cluster_id=' +this.state.cluster_id+ '&componente_id='+this.state.componente_id+'&operacion_id='+this.state.operacion_id,
          method: 'get',
          //  timeout: 8000,
          headers: {
            'Accept': '*/*',
            'Authorization': this.props.userAcceso
          }
        })
        if (res.status === 200) {
          // peticion correctamente
          if(res.data.length > 0){
            this.setState({
              msgProye:'seleccion ', 
              Proyectos: res.data });
          }else{
            this.setState({
              msgProye:'Esta llave no tiene presupuesto.'  ,Proyectos:[]});
          }
          
        }
      }
      catch (error) {
        this.getError(error.response);
      }
    }else{
      this.setState({
        msgProye:'seleccion los campos requeridos'  ,Proyectos:[]});
    
    }
  }
    
  

  listCluster() {
    return this.state.Cluster.map(row => {
      return (
        <MenuItem key={row.id} value={row.id}>{row.nombre}</MenuItem>
      );
    });
  }

  listComponente() {
    return this.state.componentes.map(row => {
      return (
        <MenuItem key={row.id} value={row.id}>{row.nombre}</MenuItem>
      );
    });
  }
  listOperation() {
    return this.state.operacions.map(row => {
      return (
        <MenuItem key={row.id} value={row.id}>{row.nombre}</MenuItem>
      );
    });
  }
  listProyecto() {
    return this.state.Proyectos.map(row => {
      return (
        <MenuItem key={row.id} value={row.id}>{row.nombre}</MenuItem>
      );
    });
  }

  render() {
    return (<div>{this.state.Mensaje ?
      <Card>
        <Grid container spacing={24}>
          <Grid item xs={6} sm={3}>

            <b>Total presupuesto:</b> {new Intl.NumberFormat('COL', {
              style: 'currency',
              currency: 'COL',
              minimumFractionDigits: 0,
              maximumFractionDigits: 0
            }).format(this.state.DatosPresupuesto.TotalPresupuesto)}
          </Grid>
          <Grid item xs={6} sm={3}>
            <b>Total comprometido:</b> {new Intl.NumberFormat('COL', {
              style: 'currency',
              currency: 'COL',
            }).format(this.state.DatosPresupuesto.comprometido)}
          </Grid>
          <Grid item xs={6} sm={3}>
            <b>Total gasto:</b>
            {new Intl.NumberFormat('COL', {
              style: 'currency',
              currency: 'COL',
            }).format(this.state.DatosPresupuesto.gasto)}
          </Grid>
          <Grid item xs={6} sm={3}>
            <b>Dinero disponible:</b> {new Intl.NumberFormat('COL', {
              style: 'currency',
              currency: 'COL',

            }).format(this.state.DatosPresupuesto.disponible)}

          </Grid>
        </Grid>
      </Card> : ''
    }
      <Grid container spacing={24}>
        <Grid item xs={12} sm={6}>
          <TextField
            error={this.state.error.servicio ? true : false}
            label={this.state.error.servicio ? this.state.error.servicio[0] : "Productos o servicios Requeridos"}
            value={this.state.servicio}
            placeholder="Productos o servicios Requeridos"
            ref="servicio"
            name="servicio"
            onChange={(e) => this.syncField(e, 'servicio')}
            fullWidth={true}
          />
          <InputLabel htmlFor="Cluster">{this.state.error.cluster ? this.state.error.cluster[0] : "Cluster"}</InputLabel>
          <Select
            error={this.state.error.cluster ? true : false}
            fullWidth={true}
            value={this.state.cluster_id}
            onChange={(e) => this.syncField(e, 'cluster_id')}
            inputProps={{
              name: 'Cluster',
              id: 'Cluster',
            }}
          >
            <MenuItem value="">
              <em>None</em>
            </MenuItem>
            {this.listCluster()}

          </Select>
          <InputLabel htmlFor="Componente">{this.state.error.componente ? this.state.error.componente[0] : "componente"}</InputLabel>
          <Select
            error={this.state.error.componente ? true : false}
            fullWidth={true}
            value={this.state.componente_id}
            onChange={(e) => this.syncField(e, 'componente_id')}
            inputProps={{
              name: 'Componente',
              id: 'Componente',
            }}
          >
            <MenuItem value="">
              <em>None</em>
            </MenuItem>
            {this.listComponente()}
          </Select>
        </Grid>
        <Grid item xs={12} sm={3}>

          <InputLabel htmlFor="operacion">{this.state.error.operacion ? this.state.error.operacion[0] : "Centro de operacin"}</InputLabel>
          <Select
            error={this.state.error.operacion ? true : false}

            fullWidth={true}
            value={this.state.operacion_id}
            onChange={(e) => this.syncField(e, 'operacion_id')}
            inputProps={{
              name: 'operacion',
              id: 'operacion',
            }}
          >
            <MenuItem value="">
              <em>None</em>
            </MenuItem>
            {this.listOperation()}
          </Select>

          <InputLabel htmlFor="proyecto">{this.state.error.proyecto ? this.state.error.proyecto[0] : "Concepto"}</InputLabel>
          <Select
            error={this.state.error.proyecto ? true : false}
            fullWidth={true}
            value={this.state.proyecto_id}
            onFocus={ (e) => this.getProyectos() }
            onChange={(e) => this.syncField(e, 'proyecto_id')}
            inputProps={{
              name: 'proyecto',
              id: 'proyecto',
            }}
          >
            <MenuItem >
              <em> {this.state.msgProye}</em>
            </MenuItem>
            {this.listProyecto()}
          </Select>
      {
/**
 * <TextField
            error={this.state.error.valorSinImpuesto ? true : false}
            label={this.state.error.valorSinImpuesto ? this.state.error.valorSinImpuesto[0] : "Precio Unitario"}
            placeholder="Ingrese el valor sin iva"
            value={this.state.valorSinImpuesto}
            onChange={(e) => this.syncField(e, 'valorSinImpuesto')}
            id="formatted-numberformat-input"
            fullWidth={true}
            InputProps={{
              inputComponent: NumberFormatCustom,
            }}
          />
 */

      }    
        </Grid>
        <Grid item xs={12} sm={3}>
          <TextField
            error={this.state.error.cantidad ? true : false}
            label={this.state.error.cantidad ? this.state.error.cantidad[0] : "Cantidad"}
            fullWidth={true}
            placeholder="Cantidad"
            value={this.state.cantidad}
            ref="cantidad"
            name="cantidad"
            onChange={(e) => this.syncField(e, 'cantidad')}
          />
          <TextField
            error={this.state.error.valorConInpuesto ? true : false}
            label={this.state.error.valorConInpuesto ? this.state.error.valorConInpuesto[0] : "Total con impuestos"}
            placeholder="Valor con con impuestos"
            value={this.state.valorConInpuesto}
            onChange={(e) => this.syncField(e, 'valorConInpuesto')}
            id="formatted-numberformat-input"
            fullWidth={true}
            InputProps={{
              inputComponent: NumberFormatCustom,
            }}
          />
        </Grid>
      </Grid>
      {this.state.action ?
        <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit}> Registrar  </Button> :
        <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar}> Modificar  </Button>
      }
      {this.listData()}
    </div>);
  }
}

export default connect(mapSteToProps)(DetallePresupuesto);